import React from 'react';
import Settings from "./Settings.view";
import {useStore} from "../../store";

export default () => {
    const updateCheckedChangeLog = useStore(state => state.updateCheckedChangeLog);
    const checkedChangeLog = useStore(state => state.userData.checkedChangeLog);

    return (<Settings checkedChangeLog={checkedChangeLog}
                      updateCheckedChangeLog={updateCheckedChangeLog}/>);
}
