import React, { useEffect, useState } from 'react';
import _get from 'lodash/get';
import { Spinner } from '@chakra-ui/react';
import initHotkeys from '../../utils/initHotkeys';

import Tafseer from '../Tafseer/index';
import VerseAudio from '../VerseAudio/VerseAudio.index';
import NextVerseButton from "../NextVerseButton/NextVerseButton.view";
import PreviousVerseButton from "../PreviousVerseButton/PreviousVerseButton.view";
import AddToFavButton from "../AddToFavButton/AddToFavButton.view";
import TranslationButton from "../TranslationButton/index";
import CustomText from "../CustomText/CustomText.view";
import useSetVerse from "../../utils/useSetVerse";
import useVerseFunctions from "../../utils/useVerseFunctions";

import './VerseContent.styles.scss';

type VerseContentProps = {
  quranData: AppState["userData"]["quranData"];
  translationFlag: AppState["userData"]["settings"]["translationFlag"];
  autoSwitchVerse?: AppState["userData"]["settings"]["autoSwitchVerse"];
  selectedLanguageKey?: AppState["userData"]["settings"]["selectedLanguageKey"],
  addNewSurahToFavourites: AppState["addNewSurahToFavourites"];
  addNewAyaToFavourites: AppState["addNewAyaToFavourites"];
  removeAyaFromFavourites: AppState["removeAyaFromFavourites"];
}

const VerseContent = ({
                        quranData,
                        translationFlag,
                        autoSwitchVerse,
                        addNewSurahToFavourites,
                        addNewAyaToFavourites,
                        removeAyaFromFavourites,
                      }: VerseContentProps) => {
  const { previousAya, nextAya } = useVerseFunctions();

  const setVerse = useSetVerse();

  const [autoSwitchVerseDoneFlag, setAutoSwitchVerseDoneFlag] = useState(false);

  const [isLoadingFlag, setIsLoadingFlag] = useState(false);

  useEffect(() => {
    const shouldFillTranslationText = translationFlag && !quranData.currentTranslationText;
    const shouldFillAyaText = !quranData.currentAyaText && quranData.currentAyaNumber;

    if (shouldFillAyaText || shouldFillTranslationText || !quranData.number) {
      //for people who comes from the old version
      loadVerse();
    }

    if (autoSwitchVerse && !autoSwitchVerseDoneFlag) {
      nextAya();
      setAutoSwitchVerseDoneFlag(true);
    }

    document.addEventListener('keydown', initHotkeys);

    return () => document.removeEventListener('keydown', initHotkeys);
    /*eslint-disable react-hooks/exhaustive-deps*/
  }, [translationFlag]);

  const addToFavourites = (): void => {
    const surahKey = `surah_${quranData.surahNumber}`;
    const ayaKey = `verse_${quranData.currentAyaNumber}`;
    const fav = quranData.favourites;

    if (!_get(fav, surahKey)) { //Surah is not found in the favourite list
      addNewSurahToFavourites(surahKey, ayaKey);
    }
    else if (!fav[surahKey][ayaKey]) {
      //Surah is already in the favourite list but the aya is not found yet
      addNewAyaToFavourites(surahKey, ayaKey);
    }
    else { //Surah and aya are already in the favourite list
      removeAyaFromFavourites(surahKey, ayaKey);
    }
  };

  const loadVerse = async () => {
    setIsLoadingFlag(true);
    await setVerse(quranData.surahNumber, quranData.currentAyaNumber);
    setIsLoadingFlag(false);
  }

  const getNextVerse = async () => {
    setIsLoadingFlag(true);
    await nextAya();
    setIsLoadingFlag(false);
  }

  const getPreviousVerse = async () => {
    setIsLoadingFlag(true);
    await previousAya();
    setIsLoadingFlag(false);
  }

  const shouldShowBasmallah = () => {
    //exclude first aya of the quran and "At-Tawba" surah because it doesn't have basmallah
    return (quranData.currentAyaNumber === 1 && quranData.number !== 1 && quranData.surahNumber !== 9);
  }

  return (
    <div className="verseContent">
      <div className="verseContent__text">
        {shouldShowBasmallah() && <CustomText className="verseContent__text--ar"
                                              fontSize="24px">   </CustomText>}
        <CustomText className="verseContent__text--ar">
          {quranData.currentAyaText}
        </CustomText>
        {translationFlag &&
        (isLoadingFlag ?
          <Spinner /> :
          <CustomText {...quranData.direction === "rtl" && { dir: 'rtl' }}>
            {quranData.currentTranslationText}
          </CustomText>)
        }
      </div>

      <div className="verseContent__iconsContent">
        <PreviousVerseButton onClickEvent={getPreviousVerse} />

        <AddToFavButton
          isActive={_get(quranData, `favourites.surah_${quranData.surahNumber}.verse_${quranData.currentAyaNumber}`)}
          onClickEvent={addToFavourites} />

        <VerseAudio />

        <TranslationButton />

        <Tafseer />

        <NextVerseButton onClickEvent={getNextVerse} />
      </div>
    </div>
  );
};

export default VerseContent;

