import React from 'react';
import {
  Button,
  VStack,
  Slider,
  SliderTrack,
  SliderFilledTrack,
  SliderThumb,
} from '@chakra-ui/react';
import { MdVolumeUp } from 'react-icons/md'
import CustomPopover from "../CustomPopover/CustomPopover.view";
import strings from "../../locales";
import CustomText from "../CustomText/CustomText.view";

type SliderKey = "adhanAudioLevel" | "verseAudioLevel";

type AudioControlsProps = {
  adhanAudioLevel: AppState["userData"]["adhanAudioLevel"];
  verseAudioLevel: AppState["userData"]["verseAudioLevel"];
  updateAudioControls: AppState["updateAudioControls"];
};

const AudioControls = ({ adhanAudioLevel, verseAudioLevel, updateAudioControls }: AudioControlsProps) => {
  const TriggerComponent = () => {
    const activeButtonStyles = {
      bgColor: "#EDF2F7",
      color: "var(--color-dark1)"
    };

    return (
      <Button bgColor={"#E2E8F0"}
              color="var(--color-dark2)"
              mx="10px"
              _hover={activeButtonStyles}
              _active={activeButtonStyles}>
        <MdVolumeUp size="20px" />
      </Button>
    )
  }

  const onChangeEnd = (key: SliderKey, value: number) => {
    updateAudioControls(key, (value / 100));
  }

  const generateAudioSlider = (key: SliderKey) => {
    const defaultValue = key === "adhanAudioLevel" ? adhanAudioLevel * 100 : verseAudioLevel * 100;

    return (
      <VStack alignItems="flexStart">
        <CustomText fontSize="sm"
                    flexShrink={0}
                    hasArabic>{strings[key]}:</CustomText>
        <Slider aria-label={key}
                step={10}
                defaultValue={defaultValue}
                onChangeEnd={value => onChangeEnd(key, value)}>
          <SliderTrack>
            <SliderFilledTrack />
          </SliderTrack>
          <SliderThumb />
        </Slider>
      </VStack>
    );
  }

  return (
    <CustomPopover TriggerComponent={TriggerComponent()}
                   size="md"
                   isLazy>
      <VStack alignItems="stretch" py={"10px"}>
        {generateAudioSlider("adhanAudioLevel")}
        {generateAudioSlider("verseAudioLevel")}
      </VStack>
    </CustomPopover>
  );
};

export default AudioControls;
