import React, { useEffect } from 'react';
import _get from 'lodash/get';
import { Box, Button, HStack, Tab, TabList, TabPanel, TabPanels, Tabs } from "@chakra-ui/react";
import { BsFillInfoCircleFill } from "react-icons/bs";
import CustomText from "../CustomText/CustomText.view";
import CustomTooltip from "../CustomTooltip/CustomTooltip.view";
import strings from "../../locales";
import azkarMassaList from "./azkar_massa.json";
import azkarSabahList from "./azkar_sabah.json";
import useLocalizeNumbers from "../../utils/useLocalizeNumbers";
import getDateString from "../../utils/getDateString";

import './AdhkarDayNight.styles.scss';

type GenerateFooter = (index: number,
                       item: {
                         repeat: number;
                         bless: string;
                       }, type: "day" | "night") => void;

type AdhkarDayNightModalProps = {
  azkarDayNightTracker: AppState["userData"]["azkarDayNightTracker"];
  azkarDayNightExpire: AppState["userData"]["azkarDayNightExpire"];
  updateAzkarDayNightTracker: AppState["updateAzkarDayNightTracker"];
  updateAzkarDayNightExpire: AppState["updateAzkarDayNightExpire"];
}

const AdhkarDayNightModal = ({
                               azkarDayNightTracker,
                               azkarDayNightExpire,
                               updateAzkarDayNightTracker,
                               updateAzkarDayNightExpire,
                             }: AdhkarDayNightModalProps) => {
  useEffect(() => {
    const newAzkarDayNightExpire = getDateString();

    if (azkarDayNightExpire !== newAzkarDayNightExpire) {
      updateAzkarDayNightTracker();
      updateAzkarDayNightExpire(newAzkarDayNightExpire);
    }
    /*eslint-disable react-hooks/exhaustive-deps*/
  }, []);

  const verseStyles = {
    isVerse: true,
    fontSize: '22px',
  }

  const { localizeNumbers } = useLocalizeNumbers();

  const updateTracker = (index: number, repetition: number, type: "day" | "night") => {
    if (!azkarDayNightTracker[type][index] || (azkarDayNightTracker[type][index] < repetition)) {
      updateAzkarDayNightTracker(index, type);
    }
  }

  const isTrackCompleted = (currentTrack: number, repetition: number) => {
    if (currentTrack === repetition) {
      return ({ colorScheme: "green" });
    }
  }

  const generateFooter: GenerateFooter = (index, item, type) => {
    const currentTrackValue = _get(azkarDayNightTracker, `${type}[${index}]`, 0);

    return (
      <HStack justify="center">
        {item.bless &&
        <CustomTooltip label={item.bless}
                       hasArabic
                       isImmediately>
          <Box cursor="pointer" mr="5px">
            <BsFillInfoCircleFill color="var(--color-dark3)" />
          </Box>
        </CustomTooltip>}
        <Button variant="outline"
                {...isTrackCompleted(currentTrackValue, item.repeat)}
                onClick={() => updateTracker(index, item.repeat, type)}>
          <CustomText hasArabic>
            {localizeNumbers(currentTrackValue)} / {localizeNumbers(item.repeat)}
          </CustomText>
        </Button>
      </HStack>
    );
  }

  return (
    <Tabs isFitted
          defaultIndex={1}>
      <TabList className='AzkarDayNight__tabList'>
        <Tab>
          <CustomText hasArabic>{strings.azkarMassa}</CustomText>
        </Tab>
        <Tab>
          <CustomText hasArabic>{strings.azkarSabah}</CustomText>
        </Tab>
      </TabList>

      <TabPanels>
        <TabPanel>
          {azkarMassaList.map((item, index) =>
            <div className="AzkarDayNight__rowContent"
                 key={index}>
              {item.opening && <CustomText isVerse>{item.opening}</CustomText>}
              <CustomText className="AzkarDayNight__zekr"
                          forceArabicMode
                          {...item.isVerse && verseStyles}>
                {item.zekr}
              </CustomText>
              {generateFooter(index, item, "night")}
            </div>)}
        </TabPanel>
        <TabPanel>
          {azkarSabahList.map((item, index) =>
            <div className="AzkarDayNight__rowContent" key={index}>
              {item.opening && <CustomText isVerse>{item.opening}</CustomText>}
              <CustomText className="AzkarDayNight__zekr"
                          forceArabicMode
                          {...item.isVerse && verseStyles}>
                {item.zekr}
              </CustomText>
              {generateFooter(index, item, "day")}
            </div>)}
        </TabPanel>
      </TabPanels>
    </Tabs>
  );
}
export default AdhkarDayNightModal;
