import React, { useEffect, useRef, useState } from 'react';
import { MdPlayArrow, MdPause, MdRepeat } from 'react-icons/md';

import VerseContentIcon from "../VerseContentIcon/VerseContentIcon.view";
import strings from "../../locales";
import useVerseFunctions from "../../utils/useVerseFunctions";
import zeroFilled from "../../utils/zeroFilled";
import { NASSER_ALQATAMI, YASSER_ALDOSSARY } from "../../utils/constants";

type VerseAudioProps = {
  quranData: AppState["userData"]["quranData"];
  verseAudioLevel: AppState["userData"]["verseAudioLevel"];
  selectedReciter: AppState["userData"]["settings"]["selectedReciter"];
  continuesPlayingVerseFlag: AppState["userData"]["settings"]["continuesPlayingVerseFlag"];
  updateSettingsProperty: AppState["updateSettingsProperty"];
};

const VerseAudio = ({
                      quranData,
                      verseAudioLevel,
                      selectedReciter,
                      continuesPlayingVerseFlag,
                      updateSettingsProperty,
                    }: VerseAudioProps) => {
  const { nextAya } = useVerseFunctions();

  const [audioURLsArray, setAudioURLsArray] = useState<string[]>([]);

  const [isAudioPlaying, setIsAudioPlaying] = useState(false);

  const [isAudioLoading, setIsAudioLoading] = useState(false);

  const audioRef: React.MutableRefObject<null | HTMLAudioElement> = useRef(null);

  useEffect(() => {
    generateAudioURLs();
    isAudioPlaying && continuesPlayingVerseFlag && playVerse();
    /*eslint-disable react-hooks/exhaustive-deps*/
  }, [quranData.number, selectedReciter]);

  useEffect(() => {
    if (audioRef.current) {
      audioRef.current.volume = verseAudioLevel
    }
  }, [verseAudioLevel]);

  const togglePlay = () => {
    if (isAudioPlaying) {
      pauseVerse();
    }
    else {
      playVerse();
    }
  }

  const playVerse = async () => {
    setIsAudioPlaying(true);
    audioRef.current?.load();
  };

  const pauseVerse = () => {
    audioRef.current && audioRef.current.pause();
    setIsAudioPlaying(false);
  };

  const onLoadedData = () => {
    setIsAudioLoading(false);

    if (isAudioPlaying && audioRef.current) {
      audioRef.current.play();
    }
  };

  const onError = () => {
    setIsAudioPlaying(false);
    setIsAudioLoading(false);

    console.log('AudioOnError: ', strings.verseAudioError);
    // toast({ description: strings.verseAudioError });
  };

  const onEnded = () => {
    if (continuesPlayingVerseFlag) {
      nextAya(true);
    }
    else {
      setIsAudioPlaying(false);
    }
  };

  const toggleContinuesPlayingVerseFlag = () => {
    const value = continuesPlayingVerseFlag === false;
    updateSettingsProperty('continuesPlayingVerseFlag', value);
  };

  const generateAudioURLs = () => {
    let urls = [
      `https://cdn.alquran.cloud/media/audio/ayah/${selectedReciter}/${quranData.number}`,
      `https://cdn.islamic.network/quran/audio/64/${selectedReciter}/${quranData.number}.mp3`,
      `https://cdn.islamic.network/quran/audio/128/${selectedReciter}/${quranData.number}.mp3`
    ];

    if (selectedReciter === NASSER_ALQATAMI || selectedReciter === YASSER_ALDOSSARY) {
      const { surahNumber, currentAyaNumber } = quranData;
      const selectedVerseNumber = `${zeroFilled(surahNumber)}${zeroFilled(currentAyaNumber)}`;

      urls = [
        `https://verse.mp3quran.net/arabic/${selectedReciter}/64/${selectedVerseNumber}.mp3`,
        `https://verse.mp3quran.net/arabic/${selectedReciter}/128/${selectedVerseNumber}.mp3`,
      ]
    }

    setAudioURLsArray(urls);
  }

  return (
    <>
      <VerseContentIcon onClick={toggleContinuesPlayingVerseFlag}
                        {...!continuesPlayingVerseFlag && { opacity: 0.4, boxShadow: "none" }}
                        tooltipText={strings.continuesPlayingVerse}>
        <MdRepeat />
      </VerseContentIcon>
      <VerseContentIcon onClick={togglePlay}
                        tooltipText={strings.playPauseAudio}
                        isLoading={isAudioLoading}
                        data-hotkeys="play">
        {isAudioPlaying ? <MdPause /> : <MdPlayArrow />}
      </VerseContentIcon>
      <audio ref={audioRef}
             onEnded={onEnded}
             onLoadedData={onLoadedData}
             onLoadStart={() => setIsAudioLoading(true)}
             onError={onError}
             onPlaying={() => setIsAudioPlaying(true)}>
        {audioURLsArray.map((url, index) => <source src={url} key={index} />)}
      </audio>
    </>
  );
};

export default VerseAudio;

