import shallow from "zustand/shallow";
import _isEmpty from 'lodash/isEmpty';
import _get from 'lodash/get';
import { useStore } from "../store";
import { ADHAN_VOICES_ARRAY } from "../components/AdhanVoice/AdhanVoice.constants";

export default () => {
  const {
    selectedAdhanVoice,
    prayerTimesObject,
  } = useStore((state: AppState) => ({
    selectedAdhanVoice: state.userData.selectedAdhanVoice,
    prayerTimesObject: state.userData.prayerTimesObject,
  }), shallow);

  const updateSelectedAdhanVoice = useStore((state: AppState) => state.updateSelectedAdhanVoice);

  const sendMessageToSetAlarms = () => {
    try {
      window.chrome.runtime.sendMessage({ name: 'setPrayerAlarms' }, () => {
        // eslint-disable-next-line no-console
        console.log('Prayer alarms are set successfully');
      });
    }
    catch (error) {
      console.warn("Couldn't set prayer alarms:", error.message);
    }
  }

  const setPrayerAlarms = (shouldForceAlarms?: boolean) => {
    //set default value for selectedAdhanVoice
    !selectedAdhanVoice && updateSelectedAdhanVoice(ADHAN_VOICES_ARRAY[1]);

    try {
      window.chrome.alarms.getAll(result => {
        const timeNow = Date.now();
        const ishaTime = _get(prayerTimesObject, ['isha', 'timeValue']);
        const isDayEnded = timeNow >= ishaTime;
        const shouldSetAlarms = !isDayEnded && (_isEmpty(result) || shouldForceAlarms);

        if (shouldSetAlarms) {
          sendMessageToSetAlarms();
        }
        else {
          console.log('Prayer alarms are already set');
          isDayEnded && console.log('Day is ended so no more alarms');
        }
      })
    }
    catch (error) {
      console.warn("Couldn't get prayer alarms:", error.message);
    }
  }

  const clearPrayerAlarms = () => {
    try {
      window.chrome.runtime.sendMessage({ name: 'clearPrayerAlarms' },
        () => {
          // eslint-disable-next-line no-console
          console.log('Prayer alarms are cleared successfully');
        });
    }
    catch (error) {
      console.warn("Couldn't clear prayer alarms:", error.message);
    }
  }

  return { setPrayerAlarms, clearPrayerAlarms };
}
