import React, { useState, useEffect, Fragment } from 'react';
import axios from 'axios';
import _isEmpty from 'lodash/isEmpty';
import _get from 'lodash/get';
import _sortBy from 'lodash/sortBy';
import { Box, Button } from '@chakra-ui/react'

import strings from '../../locales';
import PopupMenu from '../PopupMenu/PopupMenu.view';
import TabsTitles from '../TabsTitles/TabsTitles.view';
import { _AR } from '../../utils/constants';
import getVerse from '../../utils/getVerse';
import useLocalizeNumbers from '../../utils/useLocalizeNumbers';
import CustomText from "../CustomText/CustomText.view";
import useSetVerse from "../../utils/useSetVerse";

import './Favourites.styles.scss';

type FavouritesProps = {
  favouritesData: {
    [key: string]: any
  },
  selectedLanguageKey: string,
}

type TabObject = {
  label?: string,
  value: string,
  surahNumber?: number;
  ayaNumber?: number;
};

type TabsArray = Array<TabObject>;

const Favourites = ({
                      favouritesData,
                      selectedLanguageKey,
                    }: FavouritesProps) => {
  const { localizeNumbers } = useLocalizeNumbers();

  const setVerse = useSetVerse();

  const [popupFlag, setPopupFlag] = useState(false);

  const [tabsTitlesArray, setTabsTitlesArray] = useState<TabsArray>([]);

  const [selectedSurahKey, setSelectedSurahKey] = useState('');

  const [tabData, setTabData] = useState<TabsArray>([]);

  useEffect(() => {
    generateTabsTitles();
    /*eslint-disable react-hooks/exhaustive-deps*/
  }, [favouritesData, selectedLanguageKey]);

  useEffect(() => {
    generateTabData();
    /*eslint-disable react-hooks/exhaustive-deps*/
  }, [selectedSurahKey, favouritesData]);

  const generateTabsTitles = async () => {
    const arr: TabsArray = [];

    if (!_isEmpty(favouritesData)) {
      const { data } = await axios.get(`data/surah.json`);
      const orderedData = _sortBy(Object.keys(favouritesData), o => parseInt(o.split('_')[1]));

      orderedData.forEach(surahKey => {
        const surahIndex = parseInt(surahKey.split('_')[1]) - 1;
        const surahTitle = selectedLanguageKey === _AR ? data[surahIndex].name : data[surahIndex].englishName;
        const ayaLength = Object.keys(favouritesData[surahKey]).length;

        arr.push({
          label: `${surahTitle} (${localizeNumbers(ayaLength)})`,
          value: surahKey
        });
      });

      setTabsTitlesArray(arr);
      setSelectedSurahKey(Object.keys(favouritesData)[0]);
    }
  };

  const generateTabData = async () => {
    if (_get(favouritesData, selectedSurahKey)) {
      const arr: TabsArray = [];

      const orderedData = _sortBy(Object.keys(favouritesData[selectedSurahKey]), o => parseInt(o.split('_')[1]));

      orderedData.map(async (ayaKey) => {
        const surahNumber = parseInt(selectedSurahKey.split('_')[1]);
        const ayaNumber = parseInt(ayaKey.split('_')[1]);

        const obj = await getVerse(surahNumber, ayaNumber);

        arr.push({
          label: obj.currentAyaText,
          value: ayaKey,
          surahNumber,
          ayaNumber,
        });
      });

      setTimeout(() => setTabData(arr), 100);
    }
  };

  return (
    <Box className='favourites'
         display={{ base: "none", sm: "flex" }}>
      <Button variant="ghost"
              onClick={() => setPopupFlag(!popupFlag)}>
        <CustomText hasArabic>{strings.favourites}</CustomText>
      </Button>
      <PopupMenu flag={popupFlag}
                 onCancel={() => setPopupFlag(false)}
                 right>
        <Fragment>
          {_isEmpty(favouritesData) ?
            <div className='favourites__empty'>{strings.favouritesEmpty}</div>
            :
            <div className='favourites__content'>
              <TabsTitles titlesArray={tabsTitlesArray}
                          selectedTitleKey={selectedSurahKey}
                          setSelectedTitleKey={setSelectedSurahKey} />
              <ul className='favourites__tabsContent element-has-ar'>
                {tabData.map((data) =>
                  <li className="favourites__tabContent favourites__tabContent--ar"
                      key={data.label}
                      onClick={() => setVerse(data.surahNumber || 1, data.ayaNumber || 1)}>
                    {data.label}
                  </li>)}
              </ul>
            </div>}
        </Fragment>
      </PopupMenu>
    </Box>
  );
};

export default Favourites;
