import React from 'react';
import ChangeVerseModal from './ChangeVerseModal.view'
import shallow from "zustand/shallow";

import { useStore } from '../../store'

type defaultProps = {
  isOpen: boolean;
  onClose: () => void,
};

export default ({
                  isOpen,
                  onClose,
                }: defaultProps) => {
  const {
    quranData,
    selectedLanguageKey,
  } = useStore(state => ({
    quranData: state.userData.quranData,
    selectedLanguageKey: state.userData.settings.selectedLanguageKey,
  }), shallow);

  return (<ChangeVerseModal isOpen={isOpen}
                            onClose={onClose}
                            quranData={quranData}
                            selectedLanguageKey={selectedLanguageKey} />);
}
;
