import React from 'react';
import classNames from 'classnames';

import './TabsTitles.styles.scss';

type TabObject = {
  label?: string,
  value: string,
}

type TabsTitlesProps = {
  selectedTitleKey: string,
  setSelectedTitleKey: (arg0: string) => void,
  titlesArray: Array<TabObject>;
  selectedLanguageKey?: AppState["userData"]["settings"]["selectedLanguageKey"];
};

const TabsTitles = ({ titlesArray, selectedTitleKey, setSelectedTitleKey }: TabsTitlesProps) => {
  const isTabActive = (item: TabObject, index: number) => {
    return (selectedTitleKey === item.value) || (!selectedTitleKey && (index === 0));
  }

  const tabClasses = (item: TabObject, index: number) => {
    return classNames('tabsTitles__tab', `tabsTitles__tab--${index}`,
      { 'tabsTitles__tab--isActive': isTabActive(item, index) })
  };

  return (
    <ul className='tabsTitles element-has-ar'>
      {
        titlesArray.map((item, index) =>
          <li className={tabClasses(item, index)}
              key={index}
              onClick={() => setSelectedTitleKey(item.value)}>
            {item.label}
          </li>)
      }
    </ul>
  );
};

export default TabsTitles;
