import getVerse from "../utils/getVerse";
import { useStore } from "../store";
import { _INITIAL_USER_DATA, _QURAN_UTHMANI_MIN } from "./constants";
import strings from "../locales";
import useCustomToast from "./useCustomToast";

export default () => {
  const toast = useCustomToast();

  const translationFlag = useStore((state: AppState) => state.userData.settings.translationFlag);

  const selectedTranslationKey = useStore((state: AppState) => state.userData.selectedTranslationKey);

  const updateQuranData = useStore((state: AppState) => state.updateQuranData);

  const updateTranslationVerseData = useStore((state: AppState) => state.updateTranslationVerseData);

  return async (surahNumber: number, currentAyaNumber: number, customTranslationKey?: string) => {
    //get arabic verse
    const obj = await getVerse(surahNumber, currentAyaNumber);
    updateQuranData(obj);

    if (translationFlag !== false) { //get translation verse
      let translationKey = customTranslationKey || selectedTranslationKey;

      if (
        !translationKey ||
        translationKey === _QURAN_UTHMANI_MIN ||
        translationKey === 'quran-simple') {
        translationKey = _INITIAL_USER_DATA.selectedTranslationKey;
      }

      try {
        const data = await getVerse(surahNumber, currentAyaNumber, translationKey);
        updateTranslationVerseData(data.currentAyaText, data.direction);
      }
      catch {
        const description = strings.formatString(strings.fetchError, strings.translations);
        toast({ description });
      }
    }
  };
};
