import React from 'react';
import { useStore } from "../../store";
import AudioControls from "./AudioControls.view";

export default () => {
  const adhanAudioLevel = useStore(state => state.userData.adhanAudioLevel);

  const verseAudioLevel = useStore(state => state.userData.verseAudioLevel);

  const updateAudioControls = useStore(state => state.updateAudioControls);

  return (
    <AudioControls adhanAudioLevel={adhanAudioLevel ?? 1}
                   verseAudioLevel={verseAudioLevel ?? 1}
                   updateAudioControls={updateAudioControls} />
  );
};
