import React, { useEffect, useState } from 'react';
import { Button } from '@chakra-ui/react';

import CustomText from "../CustomText/CustomText.view";

type HijriDateProps = {
  selectedDateLocale: AppState["userData"]["selectedDateLocale"];
  toggleDateLocale: AppState["toggleDateLocale"];
}

const HijriDate = ({ selectedDateLocale, toggleDateLocale }: HijriDateProps) => {
  const [dateFormat, setDateFormat] = useState<string>();

  useEffect(() => {
    const date = new Date();
    const dateFormat = date.toLocaleDateString(selectedDateLocale, {
      day: 'numeric',
      month: 'long',
      year: 'numeric'
    });

    setDateFormat(dateFormat);
  }, [selectedDateLocale]);

  return (
    <Button variant="ghost"
            color="var(--color-light1)"
            fontWeight="700"
            onClick={toggleDateLocale}>
      <CustomText hasArabic
                  h={10}
                  d="flex"
                  alignItems="center"
                  px={4}>
        {dateFormat}
      </CustomText>
    </Button>
  );
};

export default HijriDate;
