import * as React from 'react';

import usePrayerTimes from '../../utils/usePrayerTimes';
import Toggle from '../Toggle/Toggle.view.jsx';
import { _AR, _EN } from '../../utils/constants';
import CustomSelect from '../CustomSelect/CustomSelect.view';
import strings from '../../locales/index';
import changeLanguage from '../../utils/changeLanguage';

type GeneralTabProps = {
  className: string;
  settings: AppState["userData"]["settings"];
  updateSettingsProperty: AppState["updateSettingsProperty"];
}

const GeneralTab = ({ className, settings, updateSettingsProperty }: GeneralTabProps) => {
  const { clearPrayerAlarms } = usePrayerTimes();

  const generalToggles = [
    {
      title: 'darkTheme',
      flag: settings.darkThemeFlag,
      onClick: () => updateSettingsProperty('darkThemeFlag', !settings.darkThemeFlag),
    },
    {
      title: 'date',
      flag: settings.dateFlag !== false, // dateFlag could be undefined for old users
      onClick: () => updateSettingsProperty('dateFlag', !settings.dateFlag),
    },
    {
      title: 'clock',
      flag: settings.clockFlag,
      onClick: () => updateSettingsProperty('clockFlag', !settings.clockFlag),
    },
    {
      title: 'clock24',
      flag: settings.clock24Flag,
      onClick: () => updateSettingsProperty('clock24Flag', !settings.clock24Flag),
    },
    {
      title: 'favourites',
      flag: settings.favouritesFlag,
      onClick: () => updateSettingsProperty('favouritesFlag', !settings.favouritesFlag),
    },
    {
      title: 'prayerTimes',
      flag: settings.prayerTimesFlag,
      onClick: () => {
        if (settings.prayerTimesFlag !== false) {
          clearPrayerAlarms();
        }
        updateSettingsProperty('prayerTimesFlag', !settings.prayerTimesFlag)
      },
    },
  ];

  const languageOptions = [
    { value: _AR, label: strings.ar },
    { value: _EN, label: strings.en },
  ];

  const getDropdownValue = languageOptions.find(obj => obj.value === settings.selectedLanguageKey) || languageOptions[1];

  const changeSelectedLanguageKey = ({ value }: { value: string }) => {
    updateSettingsProperty('selectedLanguageKey', value);
    changeLanguage(value);
  };

  return (<ul className={className}>
    <li className='popupMenu__row'>
      <div>{strings.language}</div>
      <CustomSelect options={languageOptions}
                    value={getDropdownValue}
                    onChange={changeSelectedLanguageKey} />
    </li>
    {generalToggles.map((item, index) =>
      <li className='popupMenu__row' key={index}>
        <div>
          {strings[item.title]}
        </div>
        <Toggle flag={item.flag}
                onClick={item.onClick} />
      </li>)}
  </ul>);
};

export default GeneralTab;
