import { useStore } from "../store";
import useSetVerse from "../utils/useSetVerse";
import _random from "lodash/random";
import axios from "axios";
import getVerse from "./getVerse";

type GetRandomVerseReturn = {
  randomSurahNumber?: number;
  randomAyaNumber?: number;
  shouldIgnoreRandomVerseFlag?: boolean;
};

type GetNextVerseDataReturn = {
  nextSurahNumber: number;
  nextAyaNumber: number;
};

export default () => {
  const setVerse = useSetVerse();

  const randomVerseFlag = useStore((state: AppState) => state.userData.settings.randomVerseFlag);

  const quranData = useStore((state: AppState) => state.userData.quranData);

  const getRandomVerse = async (): Promise<GetRandomVerseReturn> => {
    const randomSurahNumber = _random(1, 114);
    const result = await axios.get(`data/surah/surah_${randomSurahNumber}.json`);
    const randomAyaNumber = _random(1, result.data.numberOfAyahs);

    return ({ randomSurahNumber, randomAyaNumber });
  };

  const getNextVerseData = async (surahNumber?: number, ayaNumber?: number, shouldIgnoreRandomVerseFlag?: boolean): Promise<GetNextVerseDataReturn> => {
    let currentSurahNumber = quranData.surahNumber;
    let currentAyaNumber = quranData.currentAyaNumber;
    let currentSurahEnd = quranData.surahEnd;

    if (surahNumber && ayaNumber) {
      const { surahEnd } = await getVerse(surahNumber, ayaNumber);
      currentSurahNumber = surahNumber;
      currentAyaNumber = ayaNumber;
      currentSurahEnd = surahEnd;
    }

    if (!shouldIgnoreRandomVerseFlag && randomVerseFlag) {
      const data = await getRandomVerse();

      return ({ nextSurahNumber: data.randomSurahNumber || 1, nextAyaNumber: data.randomAyaNumber || 1 })
    }
    else if (currentSurahEnd && currentAyaNumber < currentSurahEnd) {
      const nextAyaNumber = currentAyaNumber + 1;

      return ({ nextSurahNumber: quranData.surahNumber, nextAyaNumber })
    }
    else if (currentSurahNumber !== 114) {
      const nextAyaNumber = 1;
      const nextSurahNumber = currentSurahNumber + 1;

      return ({ nextSurahNumber, nextAyaNumber })
    }
    // Go to the first surah, first aya in the Quran
    return ({ nextSurahNumber: 1, nextAyaNumber: 1 })
  };

  const nextAya = async (shouldIgnoreRandomVerseFlag?: boolean): Promise<void> => {
    const { nextSurahNumber, nextAyaNumber } = await getNextVerseData(undefined, undefined, shouldIgnoreRandomVerseFlag);
    await setVerse(nextSurahNumber, nextAyaNumber);
  };

  const previousAya = async (): Promise<void> => {
    if (randomVerseFlag) {
      const data = await getRandomVerse();

      await setVerse(data.randomSurahNumber || 1, data.randomAyaNumber || 1);
    }
    else if (quranData.currentAyaNumber === 1 && quranData.surahNumber > 1) { //Get last aya of previous surah
      const previousSurah = quranData.surahNumber - 1;

      //Get last aya of previous surah
      axios.get(`data/surah/surah_${previousSurah}.json`)
        .then(async (result) => {
          await setVerse(previousSurah, parseInt(result.data.numberOfAyahs))
        });
    }
    else if (quranData.currentAyaNumber > 1) { //Get previous aya
      const previousAyaNumber = quranData.currentAyaNumber - 1;

      await setVerse(quranData.surahNumber, previousAyaNumber);
    }
    else { //Go to the last surah and last aya in the Quran
      await setVerse(114, 6);
    }
  };

  return ({ previousAya, nextAya, getNextVerseData })
};
