import React from 'react';
import { useStore } from "../../store";
import QuickLinks from "./QuickLinks.view";

export default () => {
  const quickLinksArray = useStore(state => state.userData.quickLinksArray);

  const deleteQuickLink = useStore(state => state.deleteQuickLink);

  return <QuickLinks quickLinksArray={quickLinksArray || []}
                     deleteQuickLink={deleteQuickLink} />
}
