import React from 'react';
import {
  NumberInput,
  NumberInputField,
  NumberInputStepper,
  NumberIncrementStepper,
  NumberDecrementStepper,
} from "@chakra-ui/react"

type CustomInputProps = {
  value: number | string;
  onChange: (arg0: number | string) => void;
}

const CustomInput = ({
                       value,
                       onChange,
                     }: CustomInputProps) => {

  return (
    <NumberInput value={value}
                 size="sm"
                 onChange={onChange}>
      <NumberInputField />
      <NumberInputStepper>
        <NumberIncrementStepper />
        <NumberDecrementStepper />
      </NumberInputStepper>
    </NumberInput>
  );
};

export default CustomInput;
