import {
  _EGYPTIAN,
  _MUSLIM_WORLD_LEAGUE,
  _NORTH_AMERICA,
  _UMM_ALQURA,
} from '../components/PrayerTab/PrayerTab.constant';
import _get from 'lodash/get';

export default (name: string): string => {
  const calculationMethodNames = [
    { old: 'MWL', new: _MUSLIM_WORLD_LEAGUE },
    { old: 'ISNA', new: _NORTH_AMERICA },
    { old: 'Egypt', new: _EGYPTIAN },
    { old: 'Makkah', new: _UMM_ALQURA },
  ];
  const result = calculationMethodNames.find(item => item.old === name);

  return _get(result, 'new', name);
};
