import React from 'react';
import { useStore } from "../../store";
import Background from "./Background.view";

export default () => {
  const backgroundFlag = useStore(state => state.userData.settings.backgroundFlag);

  const selectedSolidColor = useStore(state => state.userData.settings.selectedSolidColor);

  const backgroundData = useStore(state => state.userData.backgroundData);

  return <Background backgroundFlag={backgroundFlag}
                     selectedSolidColor={selectedSolidColor}
                     backgroundData={backgroundData} />
}
