import React from 'react';
import shallow from "zustand/shallow";

import { useStore } from "../../store";
import { _INITIAL_USER_DATA } from "../../utils/constants";
import VerseAudio from './VerseAudio.view';

export default () => {
  const {
    quranData,
    verseAudioLevel,
    selectedReciter,
    continuesPlayingVerseFlag,
  } = useStore(state => ({
    quranData: state.userData.quranData,
    verseAudioLevel: state.userData.verseAudioLevel,
    selectedReciter: state.userData.settings.selectedReciter,
    continuesPlayingVerseFlag: state.userData.settings.continuesPlayingVerseFlag,
  }), shallow);

  const updateSettingsProperty = useStore(state => state.updateSettingsProperty);

  return (<VerseAudio quranData={quranData || _INITIAL_USER_DATA.quranData}
                      verseAudioLevel={verseAudioLevel ?? 1}
                      selectedReciter={selectedReciter}
                      continuesPlayingVerseFlag={continuesPlayingVerseFlag !== false}
                      updateSettingsProperty={updateSettingsProperty} />)
}
