import axios, { AxiosResponse } from 'axios';
import _get from 'lodash/get'
import { JALALAYN, MUYASSAR } from './constants';

type GetTafseer = (surahNumber: number, ayaNumber: number) => Promise<{
  surahNumber: number;
  ayaNumber: number;
  muyassar: string;
  jalalayn: string;
}>;

const getTafseerMuyassar = (surahNumber: number, ayaNumber: number): Promise<AxiosResponse> => {
  const url = `http://api.alquran.cloud/ayah/${surahNumber}:${ayaNumber}/editions/ar.muyassar`;

  return axios.get(url);
};

const getTafseerJalalayn = (surahNumber: number, ayaNumber: number): Promise<AxiosResponse> => {
  const url2 = `http://api.alquran.cloud/ayah/${surahNumber}:${ayaNumber}/editions/ar.jalalayn`;

  return axios.get(url2);
};

const getTafseer: GetTafseer = async (surahNumber, ayaNumber) => {
  const [tafseerMuyassar, tafseerJalalayn] = await Promise.all([
    getTafseerMuyassar(surahNumber, ayaNumber),
    getTafseerJalalayn(surahNumber, ayaNumber),
  ]);

  return {
    surahNumber,
    ayaNumber,
    [MUYASSAR]: _get(tafseerMuyassar, 'data.data[0].text'),
    [JALALAYN]: _get(tafseerJalalayn, 'data.data[0].text'),
  };
};

export default getTafseer;
