import React from 'react';
import {
  Modal,
  ModalBody,
  ModalHeader,
  ModalFooter,
  ModalCloseButton,
  ModalContent,
  ModalOverlay,
  ModalProps,
} from "@chakra-ui/react";

import './CustomModal.styles.scss';

type CustomModalProps = {
  header?: string | {};
  className?: string;
  children: {};
  size?: "xs" | "sm" | "md" | "lg" | "xl" | "full";
  footer?: React.ReactNode;
  isScrollInside?: boolean;
}

const CustomModal = ({
                       header,
                       className,
                       children,
                       size,
                       footer,
                       isScrollInside,
                       ...props
                     }: CustomModalProps & ModalProps) => {
  return (
    <Modal {...size && { size }}
           {...isScrollInside && { scrollBehavior: "inside" }}
           {...props}>
      <ModalOverlay />
      <ModalContent className={className}>
        {header && <ModalHeader textAlign="center"
                                borderBottomWidth="1px">
          {header}
        </ModalHeader>}
        <ModalCloseButton zIndex={2} />
        <ModalBody>
          {children}
        </ModalBody>
        {footer && <ModalFooter>
          {footer}
        </ModalFooter>}
      </ModalContent>
    </Modal>
  );
};

export default CustomModal;
