import React, { useState, useEffect } from 'react';
import StyledFirebaseAuth from 'react-firebaseui/StyledFirebaseAuth';
import * as firebase from 'firebase/app';
import _get from 'lodash/get';
import { Button, useDisclosure } from '@chakra-ui/react'

import { auth, db } from '../../firebase.js';
import { useStore } from '../../store';
import strings from '../../locales/index';
import CustomModal from '../CustomModal/CustomModal.view';
import { _INITIAL_USER_DATA, _EN, _AR } from '../../utils/constants';
import changeLanguage from '../../utils/changeLanguage';
import getDateString from "../../utils/getDateString";
import CustomText from "../CustomText/CustomText.view";
import useBackgroundFunctions from '../../utils/useBackgroundFunctions';

import './Login.styles.scss';

const uiConfig = { // Configure FirebaseUI.
  signInFlow: 'popup',
  credentialHelper: 'none',
  signInOptions: [
    firebase.auth.EmailAuthProvider.EMAIL_PASSWORD_SIGN_IN_METHOD,
  ],
};

const Login = () => {
  const { fetchNewBackground } = useBackgroundFunctions();

  const { isOpen, onOpen, onClose } = useDisclosure();

  const [isSignedIn, setIsSignedIn] = useState(false);

  const userData = useStore(state => state.userData);

  const updateAppVersion = useStore(state => state.updateAppVersion);

  const updateCheckedChangeLog = useStore(state => state.updateCheckedChangeLog);

  const updateAccountDetails = useStore(state => state.updateAccountDetails);

  const updateUID = useStore(state => state.updateUID);

  const clearAccountDetails = useStore(state => state.clearAccountDetails);

  const setState = useStore(state => state.setUserData);

  const initUserData = async (): Promise<void> => {
    const user = auth && auth.currentUser;
    let data = { ...userData };

    if (user && db) { //Get the data from the database and apply the changes
      const accountDetails = {
        email: user.email,
        displayName: user.displayName,
      };
      const docRef = db.collection('users').doc(user.uid);
      const doc = await docRef.get();

      if (!doc.exists) { //user don't have data in db yet so we will create a new record for him
        docRef.set({
          ...data,
          accountDetails,
        })
          // eslint-disable-next-line no-console
          .then(() => console.info('Document successfully written!'))
          .catch((error: object) => console.error('Error writing document: ', error));
      }
      else { //user has data in db so we gonna get this data and use it
        data = {
          ...userData,
          ...doc.data(),
        };
        const randomVerseFlag = _get(data, 'settings.randomVerseFlag');

        if (randomVerseFlag) {//don't sync verse if randomVerseFlag is true
          setState({
            ...data,
            quranData: { ...userData.quranData },
          });
        }
        else {
          setState(data); //Data is auto saved in the localStorage by the useEffect function
        }

        if (!data.accountDetails) {
          updateAccountDetails(accountDetails);
        }
      }

      if (!userData.uid) {
        updateUID(user.uid);
      }
    }

    //Apply arabic language
    const selectedLanguageKey = _get(data, 'settings.selectedLanguageKey', _EN);

    if (selectedLanguageKey === _AR) {
      changeLanguage(selectedLanguageKey);
    }

    if (_INITIAL_USER_DATA.version !== data.version) { //Check if there's a new version
      updateAppVersion(_INITIAL_USER_DATA.version);
      updateCheckedChangeLog(false);
    }

    const shouldLoadNewBackground = _get(data, 'settings.backgroundFlag', true) && _get(data, 'settings.autoChangeBackgroundFlag', true);

    if (shouldLoadNewBackground) {
      await generateNewBackground(_get(data, 'backgroundData.backgroundExpire'));
    }
  };

  useEffect(() => {
    if (userData) {
      localStorage.setItem('userData', JSON.stringify(userData));
    }
  }, [userData]);

  useEffect(() => {
    initUserData();
    /*eslint-disable react-hooks/exhaustive-deps*/
  }, [isSignedIn]);

  useEffect(() => {
    let unregisterAuthObserver: () => void;

    if (auth) {
      unregisterAuthObserver = auth.onAuthStateChanged((user) => setIsSignedIn(!!user));
      return () => unregisterAuthObserver();
    }
  }, []);

  const toggleModalFlag = (): void => {
    if (isSignedIn) {
      const result = global.confirm('Are you sure you want to logout. The app wil stop syncing your data');

      if (result && auth) {
        auth.signOut();
        setIsSignedIn(false);
        clearAccountDetails();
      }
    }
    else {
      onOpen();
    }
  };

  const generateNewBackground = async (backgroundExpire: string): Promise<void> => {
    const newBackgroundExpire = getDateString();

    if (backgroundExpire !== newBackgroundExpire) {
      await fetchNewBackground();
    }
  };

  return (
    <div className="login">
      <Button variant="ghost"
              onClick={toggleModalFlag}>
        <CustomText hasArabic>
          {!isSignedIn ? strings.login : strings.logout}
        </CustomText>
      </Button>

      <CustomModal
        isOpen={isOpen && !isSignedIn}
        header={strings.login}
        onClose={onClose}
        size="xs"
        className="element-has-ar"
      >
        <StyledFirebaseAuth
          uiConfig={uiConfig}
          firebaseAuth={auth}
        />
      </CustomModal>
    </div>
  );
};

export default Login;
