import * as React from 'react';
import shallow from 'zustand/shallow';
import Clock from './Clock.view';
import { useStore } from '../../store';

const ClockData = () => {
  const [is24Flag, clockFlag, selectedLanguageKey] = useStore(state => [
    state.userData.settings.clock24Flag,
    state.userData.settings.clockFlag,
    state.userData.settings.selectedLanguageKey,
  ], shallow);

  if (!clockFlag) {
    return null;
  }

  return <Clock is24Flag={is24Flag}
                selectedLanguageKey={selectedLanguageKey} />;
};

export default ClockData;
