import React from 'react';
import _get from 'lodash/get';
import { Box } from '@chakra-ui/react'

import './CreditAuthor.styles.scss'

type CreditAuthorProps = {
  backgroundData: AppState["userData"]["backgroundData"];
  backgroundFlag: AppState["userData"]["settings"]["backgroundFlag"];
}
const CreditAuthor = ({ backgroundData, backgroundFlag }: CreditAuthorProps) => {
  const params = '?utm_source=quran_tab&utm_medium=referral';

  if (!_get(backgroundData, 'username') || backgroundFlag === false) return null;

  return (
    <Box className='creditAuthor'
         display={{ base: "none", sm: "flex" }}
         mx={2}>
      Photo by
      <a className='creditAuthor__link'
         href={`${backgroundData.profile_url}${params}`}
         target='_blank'
         rel='noopener noreferrer'>
        {backgroundData.username}
      </a>
      on
      <a className='creditAuthor__link'
         href={`https://unsplash.com${params}`}
         target='_blank'
         rel='noopener noreferrer'>
        Unsplash
      </a>
    </Box>
  );
};

export default CreditAuthor;
