import React from 'react';
import shallow from 'zustand/shallow'

import TranslationButton from './TranslationButton.view'
import { useStore } from '../../store';
import { _INITIAL_USER_DATA } from "../../utils/constants";

export default () => {
  const [
    selectedTranslationKey,
    quranData,
    translationsExpire,
    translationsData,
    updateTranslationsData,
  ] = useStore(state => [
    state.userData.selectedTranslationKey,
    state.userData.quranData,
    state.userData.translationsExpire,
    state.userData.translationsData,
    state.updateTranslationsData,
  ], shallow);

  const updateSelectedTranslationKey = useStore(state => state.updateSelectedTranslationKey);

  const result = selectedTranslationKey || _INITIAL_USER_DATA.selectedTranslationKey;

  return (
    <TranslationButton selectedTranslationKey={result}
                       quranData={quranData}
                       translationsExpire={translationsExpire}
                       translationsData={translationsData || []}
                       updateSelectedTranslationKey={updateSelectedTranslationKey}
                       updateTranslationsData={updateTranslationsData} />
  );
}
