import React, { useEffect, useState } from 'react';
import { _AR, _AR_LOCALE, _EN_LOCALE } from "../../utils/constants";

import './Clock.styles.scss';

type ClockProps = {
  is24Flag?: boolean;
  selectedLanguageKey?: string;
}

const Clock = ({ is24Flag, selectedLanguageKey }: ClockProps) => {
  const [clockFormat, setClockFormat] = useState('00:00');

  let timeoutVar: NodeJS.Timeout;

  useEffect(() => {
    timeoutVar && clearTimeout(timeoutVar);
    generateTime();

    return () => clearTimeout(timeoutVar);
    /*eslint-disable react-hooks/exhaustive-deps*/
  }, [is24Flag, selectedLanguageKey]);

  const generateTime = () => {
    const date = new Date();
    const locale = selectedLanguageKey === _AR ? _AR_LOCALE : _EN_LOCALE;
    const data = date.toLocaleTimeString(locale, {
      // @ts-ignore
      hourCycle: is24Flag ? "h23" : "h12",
      hour: '2-digit',
      minute: '2-digit',
    });

    setClockFormat(data);
    timeoutVar = setTimeout(() => generateTime(), 60000);
  };

  return (
    <div className="Clock element-has-ar">
      {clockFormat.split(' ')[0]}
      {!is24Flag && <span className="Clock__text">{clockFormat.split(' ')[1]}</span>}
    </div>
  );
};

export default Clock;
