import React from 'react';
import { useStore } from '../../store';
import AdhkarDayNight from './AdhkarDayNight.view';

export default () => {
  const azkarDayNightTracker = useStore(state => state.userData.azkarDayNightTracker);

  const azkarDayNightExpire = useStore(state => state.userData.azkarDayNightExpire);

  const updateAzkarDayNightTracker = useStore(state => state.updateAzkarDayNightTracker);

  const updateAzkarDayNightExpire = useStore(state => state.updateAzkarDayNightExpire);

  return (
    <AdhkarDayNight azkarDayNightTracker={azkarDayNightTracker}
                    azkarDayNightExpire={azkarDayNightExpire}
                    updateAzkarDayNightTracker={updateAzkarDayNightTracker}
                    updateAzkarDayNightExpire={updateAzkarDayNightExpire} />
  );
}
