import React from 'react';
import { useStore } from '../../store';

import Tafseer from "./Tafseer.view";
import shallow from 'zustand/shallow'

export default () => {
  const [
    surahNumber,
    currentAyaNumber
  ] = useStore(state => [
    state.userData.quranData.surahNumber,
    state.userData.quranData.currentAyaNumber,
  ], shallow);

  return (
    <Tafseer surahNumber={surahNumber}
             ayaNumber={currentAyaNumber} />
  );
}
