export default (e: { key: string; }) => {
  const isPopupOpen = document.querySelector('.popupMenu--isActive');
  const isChakraOpen = document.querySelector('.chakra-portal');

  if (!isChakraOpen && !isPopupOpen) {
    if (e.key === 'ArrowLeft') {
      const prevBtn = document.querySelector('.verseContent__iconsContent [data-hotkeys="previous"]');
      // @ts-ignore
      prevBtn && prevBtn.click();
    }
    else if (e.key === 'ArrowRight') {
      const nextBtn = document.querySelector('.verseContent__iconsContent [data-hotkeys="next"]');
      // @ts-ignore
      nextBtn && nextBtn.click();
    }
    else if (e.key === 'l') {
      const favBtn = document.querySelector('.verseContent__iconsContent [data-hotkeys="favourite"]');
      // @ts-ignore
      favBtn.click();
    }
    else if (e.key === ' ') {
      const playBtn = document.querySelector('.verseContent__iconsContent [data-hotkeys="play"]');
      // @ts-ignore
      playBtn && playBtn.click();
    }
    else if (e.key === 't') {
      const tafseerBtn = document.querySelector('.verseContent__iconsContent [data-hotkeys="tafseer"]');
      // @ts-ignore
      tafseerBtn && tafseerBtn.click();
    }
  }
}
