import React from 'react';
import shallow from "zustand/shallow";
import _get from 'lodash/get'

import BackgroundTab from './BackgroundTab.view';
import { useStore } from "../../store";

type Props = {
  className: string;
}

export default ({ className }: Props) => {
  const {
    backgroundFlag,
    autoChangeBackgroundFlag,
    selectedSolidColor,
    backgroundList,
  } = useStore(state => ({
    backgroundFlag: state.userData.settings.backgroundFlag,
    autoChangeBackgroundFlag: state.userData.settings.autoChangeBackgroundFlag,
    selectedSolidColor: state.userData.settings.selectedSolidColor,
    backgroundList: _get(state.userData, 'backgroundData.backgroundList'),
  }), shallow);

  const toggleBackgroundFlag = useStore(state => state.toggleBackgroundFlag);

  const toggleAutoChangeBackgroundFlag = useStore(state => state.toggleAutoChangeBackgroundFlag);

  const updateSelectedSolidColor = useStore(state => state.updateSelectedSolidColor);

  const updateBackgroundData = useStore(state => state.updateBackgroundData);

  return (<BackgroundTab className={className}
                         backgroundFlag={backgroundFlag !== false}
                         autoChangeBackgroundFlag={autoChangeBackgroundFlag !== false}
                         selectedSolidColor={selectedSolidColor}
                         backgroundList={backgroundList}
                         toggleBackgroundFlag={toggleBackgroundFlag}
                         toggleAutoChangeBackgroundFlag={toggleAutoChangeBackgroundFlag}
                         updateSelectedSolidColor={updateSelectedSolidColor}
                         updateBackgroundData={updateBackgroundData} />);
}
