import React from 'react';
import { useStore } from '../../store';
import AdhanVoice from "./AdhanVoice.view";

export default () => {
  const selectedAdhanVoice = useStore(state => state.userData.selectedAdhanVoice);

  const adhanAudioLevel = useStore(state => state.userData.adhanAudioLevel);

  const updateSelectedAdhanVoice = useStore(state => state.updateSelectedAdhanVoice);

  return (<AdhanVoice selectedAdhanVoice={selectedAdhanVoice}
                      adhanAudioLevel={adhanAudioLevel ?? 1}
                      updateSelectedAdhanVoice={updateSelectedAdhanVoice} />);

};
