import * as React from 'react';
import { _CHANGE_LOG } from './WhatsNewTab.constant';
import CustomText from "../CustomText/CustomText.view";

type props = {
  className: string
}

const WhatsNewTab = ({ className }: props) => {
  return (<ul className={className}>
    {_CHANGE_LOG.map((item, index) =>
      <li className='popupMenu__row popupMenu__whatsNewContent'
          key={index}>
        <div className='popupMenu__whatsNewDate'>{item.date}</div>
        <ul className='popupMenu__whatsNewChanges'>
          {item.changes.map((change, index) =>
            <li className='popupMenu__whatsNewChangesRow'
                key={index}>
              <div className='popupMenu__whatsNewValue'
                   dir="auto">
                <CustomText forceArabicMode>
                  {change.value}
                </CustomText>
              </div>
              <div className={`popupMenu__whatsNewType popupMenu__whatsNewType--${change.type}`}>
                {change.type}
              </div>
            </li>,
          )}
        </ul>
      </li>,
    )}
  </ul>);
};

export default WhatsNewTab;
