import React from 'react';
import { Box } from "@chakra-ui/react";
import _get from "lodash/get";

import bgImg from "../../assets/imgs/bg.jpg";
import { _INITIAL_USER_DATA } from "../../utils/constants";

import './Background.styles.scss';

type BackgroundProps = {
  backgroundFlag: AppState["userData"]["settings"]["backgroundFlag"];
  selectedSolidColor: AppState["userData"]["settings"]["selectedSolidColor"];
  backgroundData: AppState["userData"]["backgroundData"];
}
const Background = ({
                      backgroundFlag,
                      selectedSolidColor,
                      backgroundData,
                    }: BackgroundProps) => {
  const getBackground = () => {
    if (backgroundFlag !== false) {
      return { backgroundImage: `url(${_get(backgroundData, 'backgroundUrl', bgImg)})` };
    }
    return { backgroundColor: selectedSolidColor || _INITIAL_USER_DATA.settings.selectedSolidColor };
  }

  return (
    <Box className="Background"
         {...getBackground()} />
  );
}

export default Background;
