import React from 'react';
import { Tooltip, TooltipProps } from "@chakra-ui/react";

type CustomTooltipProps = {
  children: {};
  hasArabic?: boolean;
  isImmediately?: boolean;
}

const arabicProps = {
  fontFamily: "var(--ar-font-family)",
  dir: 'rtl'
};

const CustomTooltip = ({ children, hasArabic, isImmediately, ...props }: CustomTooltipProps & TooltipProps) => {
  return (
    <Tooltip label={props.label}
             aria-label="A tooltip"
             fontSize="sm"
             fontWeight="medium"
             hasArrow
             {...!isImmediately && { openDelay: 500 }}
             {...hasArabic && arabicProps}>
      {children}
    </Tooltip>
  );
};

export default CustomTooltip;
