import React from 'react';
import { Box, Select } from '@chakra-ui/react';

import strings from '../../locales';
import { _INITIAL_USER_DATA } from '../../utils/constants';
import recitersDataArray from './recitersDataArray.const';
import Toggle from '../Toggle/Toggle.view.jsx';


type VerseTabProps = {
  className: string,
  settings: AppState["userData"]["settings"],
  updateSettingsProperty: (arg1: string, arg2: {}) => void,
};

const VerseTab = ({ settings, className, updateSettingsProperty }: VerseTabProps) => {
  const verseToggles = [
    {
      title: 'showVerse',
      flag: settings.verseFlag !== false,
      onClick: () => updateSettingsProperty('verseFlag', !settings.verseFlag),
    },
    {
      title: 'showTranslation',
      flag: settings.translationFlag !== false,
      onClick: () => {
        const flagValue = settings.translationFlag !== false;
        updateSettingsProperty('translationFlag', !flagValue)
      },
    },
    {
      title: 'autoSwitchVerse',
      flag: settings.autoSwitchVerse,
      onClick: () => updateSettingsProperty('autoSwitchVerse', !settings.autoSwitchVerse),
    },
    {
      title: 'randomVerse',
      flag: settings.randomVerseFlag,
      onClick: () => updateSettingsProperty('randomVerseFlag', !settings.randomVerseFlag),
    },
    {
      title: 'continuesPlayingVerse',
      flag: settings.continuesPlayingVerseFlag !== false,
      onClick: () => {
        const value = settings.continuesPlayingVerseFlag === false;
        updateSettingsProperty('continuesPlayingVerseFlag', value);
      },
    },
  ];

  const changeRecitersOptions = (value: string) => {
    updateSettingsProperty('selectedReciter', value);
  };

  const selectedReciterValue = settings.selectedReciter || _INITIAL_USER_DATA.settings.selectedReciter

  return (
    <ul className={className}>
      <li className='popupMenu__row'>
        <Box mr={24}>{strings.reciters}</Box>
        <Select defaultValue={selectedReciterValue}
                onChange={event => changeRecitersOptions(event.target.value)}>
          {recitersDataArray.map(identifier => <option key={identifier}
                                                       value={identifier}>
            {strings[identifier]}
          </option>)}
        </Select>
      </li>
      {
        verseToggles.map((item, index) => <li className='popupMenu__row' key={index}>
          <div>
            {strings[item.title]}
          </div>
          <Toggle flag={item.flag}
                  onClick={item.onClick} />
        </li>)
      }
    </ul>
  );
};

export default VerseTab;
