import React from 'react';
import { Text, TextProps } from '@chakra-ui/react'

import { useStore } from '../../store'
import { _AR } from "../../utils/constants";

type CustomTextProps = {
  children?: {};
  hasArabic?: boolean;
  forceArabicMode?: boolean;
  isVerse?: boolean;
};

const CustomText = ({ children, hasArabic, forceArabicMode, isVerse, ...props }: TextProps & CustomTextProps) => {
  const selectedLanguageKey = useStore((store: AppState) => store.userData.settings.selectedLanguageKey);

  const getProps = () => {
    if (isVerse) {
      return ({
        fontFamily: 'var(--verse-font-family)',
        pt: 1,
        dir: 'rtl'
      })
    }
    else if (forceArabicMode || (hasArabic && (selectedLanguageKey === _AR))) {
      return ({
        fontFamily: 'var(--ar-font-family)',
        pt: 1,
        dir: 'rtl'
      })
    }

    return {};
  };

  return (
    <Text {...getProps()}
          {...props}>
      {children}
    </Text>
  );
};

export default CustomText;
