import React from 'react';
import classNames from 'classnames';

import './PopupMenu.styles.scss';

type PopupMenuProps = {
  flag: boolean;
  children: JSX.Element;
  onCancel: () => void;
  right?: boolean,
};

const PopupMenu = ({ flag, onCancel, children, right }: PopupMenuProps) => {
  const popupClasses = classNames('popupMenu popupMenu__bottom', {
    'popupMenu--isActive': flag,
    'popupMenu--isRight': right,
  });

  return (
    <div className={popupClasses}>
      <div className="popupMenu__overlay"
           onClick={onCancel} />
      <div className='popupMenu__body'>
        {children}
      </div>
    </div>
  );
};

export default PopupMenu;
