import _get from "lodash/get";
import { useStore } from "../store";
import { _AR } from "./constants";

export default () => {
  const selectedLanguageKey = useStore((state: AppState) => _get(state.userData, 'settings.selectedLanguageKey', 'en'));

  const localizeNumbers = (value: number | string = 0) => {
    let result = value;

    if (selectedLanguageKey === _AR) {
      const id = ['', '', '', '', '', '', '', '', '', ''];
      result = (value.toString()).replace(/[0-9]/g, (index: string) => id[+index]);
    }

    return result;
  };

  return ({ localizeNumbers });
};
