import React from 'react';
import { Box, Select } from '@chakra-ui/react';

import strings from '../../locales';
import Toggle from '../Toggle/Toggle.view.jsx';
import {
  _MUSLIM_WORLD_LEAGUE,
  _EGYPTIAN,
  _KARACHI,
  _NORTH_AMERICA,
  _UMM_ALQURA,
  _DUBAI,
  _QATAR,
  _KUWAIT,
  _MOONSIGHTING_COMMITTEE,
  _SINGAPORE,
  _TURKEY,
  _TEHRAN,
} from './PrayerTab.constant';
import convertOldCalculationMethodNames from "../../utils/convertOldCalculationMethodNames";
import SelectLocation from "../SelectLocation/SelectLocation.index";
import AdhanVoice from "../AdhanVoice/AdhanVoice.index";
import ManuelPrayerTimesSettings from "../ManuelPrayerTimesSettings/ManuelPrayerTimesSettings.index";

type PrayerTabProps = {
  className: string;
  prayerMethod: AppState['userData']['settings']['prayerTimes']['prayerMethod'];
  dayNightFlag: AppState['userData']['settings']['prayerTimes']['dayNightFlag'];
  playDoaaFlag: AppState['userData']['settings']['playDoaaFlag'];
  location: AppState['userData']['location'];
  updatePrayerMethod: AppState['updatePrayerMethod'];
  toggleDayNightFlag: AppState['toggleDayNightFlag'];
  togglePlayDoaaFlag: AppState['togglePlayDoaaFlag'];
  updateLocation: AppState['updateLocation'];
}

const PrayerTab = ({
                     className,
                     prayerMethod,
                     dayNightFlag,
                     playDoaaFlag,
                     updatePrayerMethod,
                     toggleDayNightFlag,
                     togglePlayDoaaFlag,
                   }: PrayerTabProps) => {
    const calculationMethodOptions = [
      { value: _MUSLIM_WORLD_LEAGUE, label: strings['calculationMethodMwl'] },
      { value: _NORTH_AMERICA, label: strings['calculationMethodIsna'] },
      { value: _EGYPTIAN, label: strings['calculationMethodEgypt'] },
      { value: _UMM_ALQURA, label: strings['calculationMethodMakkah'] },
      { value: _KARACHI, label: strings['calculationMethodKarachi'] },
      { value: _DUBAI, label: strings['calculationMethodDubai'] },
      { value: _QATAR, label: strings['calculationMethodQatar'] },
      { value: _KUWAIT, label: strings['calculationMethodKuwait'] },
      { value: _MOONSIGHTING_COMMITTEE, label: strings['calculationMethodMoonsightingCommittee'] },
      { value: _SINGAPORE, label: strings['calculationMethodSingapore'] },
      { value: _TURKEY, label: strings['calculationMethodTurkey'] },
      { value: _TEHRAN, label: strings['calculationMethodTehran'] },
    ];

    const selectStyles = { maxW: "240px" };

    const getSelectedCalculationMethod = () => {
      const result = convertOldCalculationMethodNames(prayerMethod);
      const selectedPrayerMethod = calculationMethodOptions.find(obj => obj.value === result) || calculationMethodOptions[0];

      return selectedPrayerMethod.value;
    };

    return (
      <ul className={className}>
        <SelectLocation />
        <li className='popupMenu__row'>
          <Box mr={12}>
            {strings.calculationMethod}
          </Box>
          <Select defaultValue={getSelectedCalculationMethod()}
                  {...selectStyles}
                  onChange={event => updatePrayerMethod(event.target.value)}>
            {calculationMethodOptions.map(item => <option key={item.value}
                                                          value={item.value}>{item.label}</option>)}
          </Select>
        </li>
        <AdhanVoice />
        <li className='popupMenu__row'>
          <div>
            {strings.daylightSavingTime}
          </div>
          <Toggle flag={dayNightFlag}
                  onClick={toggleDayNightFlag} />
        </li>
        <li className='popupMenu__row'>
          <div>
            {strings.playDoaa}
          </div>
          <Toggle flag={playDoaaFlag}
                  onClick={togglePlayDoaaFlag} />
        </li>
        <ManuelPrayerTimesSettings />
      </ul>);
  }
;

export default PrayerTab;
