import React, { useEffect, useState } from 'react';
import { MdTranslate } from 'react-icons/md';
import { Box, Button, Text, useDisclosure } from '@chakra-ui/react'
import axios from 'axios';
import _sortBy from 'lodash/sortBy'
import ReactGA from "react-ga";

import useCustomToast from '../../utils/useCustomToast'
import strings from "../../locales";
import CustomModal from "../CustomModal/CustomModal.view";
import CustomSelect from "../CustomSelect/CustomSelect.view";
import { TranslationButtonProps, RawTranslationObject, TranslationData, TranslationObject } from "./TranslationButton";
import getDateString from "../../utils/getDateString";
import VerseContentIcon from "../VerseContentIcon/VerseContentIcon.view";
import useSetVerse from "../../utils/useSetVerse";

const TranslationButton = ({
                             quranData,
                             selectedTranslationKey,
                             translationsExpire,
                             translationsData,
                             updateSelectedTranslationKey,
                             updateTranslationsData,
                           }: TranslationButtonProps) => {
  const { isOpen, onOpen, onClose } = useDisclosure();

  const [isLoading, setIsLoading] = useState(false);

  const toast = useCustomToast();

  const setVerse = useSetVerse();

  const [selectedTranslationValue, setSelectedTranslationValue] = useState<string>(selectedTranslationKey);

  const [translationArray, setTranslationArray] = useState<TranslationData | []>([]);

  useEffect(() => {
    getTranslationData();
    /*eslint-disable react-hooks/exhaustive-deps*/
  }, [])

  const buildTranslationArray = (data: RawTranslationObject[]): TranslationData => {
    const orderedList = _sortBy(data, ["language"]);
    return orderedList
      .filter((obj: RawTranslationObject) => obj.language !== "ar")
      .map((obj: RawTranslationObject) => ({
        label: `${obj.language} - ${obj.englishName}`,
        value: obj.identifier,
      }));
  };

  const fetchTranslationData = async (): Promise<TranslationData | []> => {
    const url = "http://api.alquran.cloud/v1/edition?type=translation&format=text";
    const result = await axios.get(url);

    try {
      if (result.data.code === 200) {
        return buildTranslationArray(result.data.data);
      }
      else {
        const description = strings.formatString(strings.fetchError, strings.translations);
        // toast({ description });
        console.log('fetchTranslationData: ', description);
      }
    }
    catch {
      const description = strings.formatString(strings.fetchError, strings.translations);
      // toast({ description });
      console.log('fetchTranslationData: ', description);
    }

    return [];
  }

  const getTranslationData = async () => {
    const translationsExpiryDate = getDateString();
    let result = [...translationsData];

    if (translationsExpiryDate !== translationsExpire) {
      result = await fetchTranslationData();
      updateTranslationsData(translationsExpiryDate, result);
    }

    setTranslationArray(result);
  };

  const getSelectedTranslation = () => {
    const result = translationArray.find(obj => obj.value === selectedTranslationValue);

    return result || {};
  }

  const changeTranslationValue = (obj: TranslationObject) => {
    setSelectedTranslationValue(obj.value);
  };

  const submitNewTranslation = async () => {
    if (selectedTranslationValue !== selectedTranslationKey) {
      setIsLoading(true);
      try {
        await setVerse(quranData.surahNumber, quranData.currentAyaNumber, selectedTranslationValue);
        updateSelectedTranslationKey(selectedTranslationValue);
      }
      catch {
        const description = strings.formatString(strings.fetchError, strings.translations);
        toast({ description });
      }
      setIsLoading(false);
      ReactGA.event({
        category: "Translation Button",
        action: `${selectedTranslationValue}.clicked`,
      });
    }
    onClose();
  }

  return (
    <>
      <VerseContentIcon onClick={onOpen}
                        tooltipText={strings.translation}>
        <MdTranslate />
      </VerseContentIcon>
      <CustomModal isOpen={isOpen}
                   onClose={onClose}
                   header={<Text className="element-has-ar">{strings.translations}</Text>}
                   footer={<Button onClick={submitNewTranslation}
                                   className="element-has-ar"
                                   isLoading={isLoading}
                                   loadingText={strings.apply}>{strings.apply}</Button>}
                   size="xs">
        <Box mt="var(--default-padding)">
          <CustomSelect options={translationArray}
                        value={getSelectedTranslation()}
                        onChange={changeTranslationValue}
                        isSearchable
                        fullWidth />
        </Box>
      </CustomModal>
    </>
  );
};

export default TranslationButton;

