import React from 'react';

import Adhkar from "../Adhkar/Adhkar.view";
import AudioControls from "../AudioControls/AudioControls.index";
import QuickLinks from "../QuickLinks/QuickLinks.index";
import HijriDate from '../HijriDate/HijriDate.index';
import VerseDetails from '../VerseDetails/index';
import isChrome from "../../utils/isChrome";

import './Header.style.scss';

type HeaderProps = {
  dateFlag?: boolean;
}

const Header = ({ dateFlag }: HeaderProps) => (
  <header className="Header">
    <VerseDetails />
    {isChrome && <QuickLinks />}
    <div className="Header__topRight">
      <Adhkar />
      {dateFlag !== false && <HijriDate />}
      <AudioControls />
    </div>
  </header>
);

export default Header;
