import React, { useEffect } from 'react';
import { useColorMode, Box } from "@chakra-ui/react";

import initGA from "./utils/initGA";
import { useStore } from './store';
import Clock from "./components/Clock/index";
import Header from "./components/Header/index";
import Login from "./components/Login/Login.view";
import Favourites from "./components/Favourites/index";
import CreditAuthor from "./components/CreditAuthor/index";
import VerseContent from "./components/VerseContent/index";
import PrayerTimes from "./components/PrayerTimes/PrayerTimes.index";
import Settings from "./components/Settings/index";
import AboutMe from "./components/AboutMe/AboutMe.view";
import Background from "./components/Background/Background.index";

import './App.scss';

function App() {
  useEffect(() => {
    initGA();
  }, []);

  const { setColorMode } = useColorMode();

  const darkThemeFlag = useStore(state => state.userData.settings.darkThemeFlag);

  useStore(state => state.userData.settings.selectedLanguageKey);

  useEffect(() => {
    darkThemeFlag ? setColorMode("dark") : setColorMode("light");
    /*eslint-disable react-hooks/exhaustive-deps*/
  }, [darkThemeFlag]);

  return (
    <Box className="App">
      <Background />
      <Header />
      <div className="App__body">
        <Clock />
        <VerseContent />
        <PrayerTimes />
      </div>
      <footer className="App__footer">
        <div className="App__footerContent">
          <Settings />
          <Login />
          <CreditAuthor />
        </div>
        <div className="App__footerContent">
          <Favourites />
          <AboutMe />
        </div>
      </footer>
    </Box>
  );
}

export default App;
