import create from 'zustand';
import produce from 'immer';
import _get from 'lodash/get';
// import { devtools } from 'zustand/middleware';

import { _AR_LOCALE, _EN_LOCALE, _INITIAL_USER_DATA } from './utils/constants';
import * as firebase from 'firebase/app';
import { db } from './firebase';

const data = JSON.parse(localStorage.getItem('userData') as string);

const updatePropertyDB = async (obj: string | any, shouldDelete?: boolean) => {
  // const user = auth && auth.currentUser;
  const userData = JSON.parse(localStorage.getItem('userData') as string)
  const uid = _get(userData, 'uid');

  if (uid && db && obj) {
    const docRef = db.collection('users').doc(uid);

    if (shouldDelete) {
      await docRef.update({
        [obj]: firebase.firestore.FieldValue.delete(),
        [`untrackedValues.${obj}`]: true,
      });
    }
    else {
      await docRef.update({
        ...obj,
      });
    }
  }
};

// export const [useStore, api] = create(devtools(store, 'userData App Store'));
export const useStore = create<AppState>((set: any, get: any) => ({
  userData: data || _INITIAL_USER_DATA,
  setUserData: value => set(produce(draft => {//don't update db here. we already do that in Login component
    draft.userData = value;
  }), 'setUserData'),
  updateWeatherObject: value => set(produce(draft => {
    draft.userData.settings.weatherObject = value;
    updatePropertyDB({ 'settings.weatherObject': value });
  }), 'updateWeatherObject'),
  updateBackgroundData: (value) => set(produce(draft => {
    const key = 'backgroundData';
    const untrackedValues = get().userData.untrackedValues;
    const isPropertyUntracked = _get(untrackedValues, key);

    draft.userData.backgroundData = {
      ...draft.userData.backgroundData,
      ...value,
    };
    !isPropertyUntracked && updatePropertyDB(key, true);
  }), 'updateBackgroundData'),
  updateSettingsProperty: (key, value) => set(produce(draft => {
    draft.userData.settings[key] = value;
    updatePropertyDB({ [`settings.${key}`]: value });
  }), 'updateSettingsProperty'),
  addNewSurahToFavourites: (surahKey, ayaKey) => set(produce(draft => { /*Add new surah(group) to the favourite list*/
    draft.userData.quranData.favourites[surahKey] = { [ayaKey]: true };
    updatePropertyDB({ [`quranData.favourites.${surahKey}`]: { [ayaKey]: true } });
  }), 'addNewSurahToFavourites'),
  addNewAyaToFavourites: (surahKey, ayaKey) => set(produce(draft => {
    draft.userData.quranData.favourites[surahKey][ayaKey] = true;
    updatePropertyDB({ [`quranData.favourites.${surahKey}.${ayaKey}`]: true });
  }), 'addNewAyaToFavourites'),
  removeAyaFromFavourites: (surahKey, ayaKey) => set(produce(draft => {
    delete draft.userData.quranData.favourites[surahKey][ayaKey];
    !Object.keys(draft.userData.quranData.favourites[surahKey]).length && delete draft.userData.quranData.favourites[surahKey];
    updatePropertyDB({ [`quranData.favourites.${surahKey}.${ayaKey}`]: firebase.firestore.FieldValue.delete() });
  }), 'removeAyaFromFavourites'),
  updateQuranData: (data) => set(produce(draft => {
    draft.userData.quranData = {
      favourites: { ...draft.userData.quranData.favourites },
      ...data
    };
    if (!draft.userData.settings.randomVerseFlag) {
      updatePropertyDB({
        'quranData.surahNumber': data.surahNumber,
        'quranData.surahName': data.surahName,
        'quranData.surahNameAr': data.surahNameAr,
        'quranData.surahEnd': data.surahEnd,
        'quranData.currentAyaNumber': data.currentAyaNumber,
        'quranData.currentAyaText': data.currentAyaText,
        'quranData.number': data.number,
      });
    }
  }), 'updateQuranData'),
  updateTranslationVerseData: (currentTranslationText, direction) => set(produce(draft => {
    draft.userData.quranData.currentTranslationText = currentTranslationText;
    draft.userData.quranData.direction = direction;
    if (!draft.userData.settings.randomVerseFlag) {
      updatePropertyDB({
        'quranData.currentTranslationText': currentTranslationText,
        'quranData.direction': direction
      });
    }
  }), 'updateTranslationVerseData'),
  toggleDayNightFlag: () => set(produce(draft => {
    const value = !draft.userData.settings.prayerTimes.dayNightFlag;
    draft.userData.settings.prayerTimes.dayNightFlag = value;
    updatePropertyDB({ 'settings.prayerTimes.dayNightFlag': value });
  }), 'toggleDayNightFlag'),
  updatePrayerMethod: (prayerMethod) => set(produce(draft => {
    draft.userData.settings.prayerTimes.prayerMethod = prayerMethod;
    updatePropertyDB({ 'settings.prayerTimes.prayerMethod': prayerMethod });
  }), 'updatePrayerMethod'),
  updateManuelPrayerTime: (key, value) => set(produce(draft => {
    let parsedValue = value;

    if (typeof parsedValue === "string") {
      parsedValue = parseInt(parsedValue);
    }
    if (draft.userData.settings.manualPrayerTimes) {
      draft.userData.settings.manualPrayerTimes[key] = parsedValue;
    }
    else {
      draft.userData.settings.manualPrayerTimes = {
        ..._INITIAL_USER_DATA.settings.manualPrayerTimes,
        [key]: parsedValue,
      };
    }

    updatePropertyDB({ [`settings.manualPrayerTimes.${key}`]: parsedValue });
  }), 'updateManuelPrayerTime'),
  toggleTodoFocusFlag: () => set(produce(draft => {
    const value = !draft.userData.todoData.todoFocusFlag;
    draft.userData.todoData.todoFocusFlag = value;
    updatePropertyDB({ 'todoData.todoFocusFlag': value });
  }), 'toggleTodoFocusFlag'),
  toggleNotificationAudioFlag: () => set(produce(draft => {//local only. no need for db.
    draft.userData.settings.notificationAudioFlag = !draft.userData.settings.notificationAudioFlag;
  }), 'toggleNotificationAudioFlag'),
  toggleCompletedTodoFlag: () => set(produce(draft => {
    const value = !draft.userData.todoData.completedTodoFlag;
    draft.userData.todoData.completedTodoFlag = value;
    updatePropertyDB({ 'todoData.completedTodoFlag': value });
  }), 'toggleCompletedTodoFlag'),
  updateRecitersData: (recitersExpire, recitersData) => set(produce(draft => {
    const key = 'settings.recitersData';
    const untrackedValues = get().userData.untrackedValues;
    const isPropertyUntracked = _get(untrackedValues, key);

    draft.userData.settings.recitersExpire = recitersExpire;
    draft.userData.settings.recitersData = recitersData;

    if (!isPropertyUntracked) {
      updatePropertyDB(key, true);
      updatePropertyDB('settings.recitersExpire', true);
    }
  }), 'updateRecitersData'),
  updateTranslationsData: (translationsExpire, translationsData) => set(produce(draft => {
    const key = 'translationsData';
    const untrackedValues = get().userData.untrackedValues;
    const isPropertyUntracked = _get(untrackedValues, key);

    draft.userData.translationsExpire = translationsExpire;
    draft.userData.translationsData = translationsData;

    if (!isPropertyUntracked) {
      updatePropertyDB(key, true);
      updatePropertyDB('translationsExpire', true);
    }
  }), 'updateTranslationsData'),
  updateCheckedChangeLog: (checkedChangeLog) => set(produce(draft => {
    draft.userData.checkedChangeLog = checkedChangeLog;
    updatePropertyDB({ 'checkedChangeLog': checkedChangeLog });
  }), 'updateCheckedChangeLog'),
  updateAppVersion: (value) => set(produce(draft => {
    draft.userData.version = value;
    updatePropertyDB({ 'version': value });
  }), 'updateAppVersion'),
  updateSelectedTranslationKey: (value) => set(produce(draft => {
    draft.userData.selectedTranslationKey = value;
    updatePropertyDB({ 'selectedTranslationKey': value });
  }), 'updateSelectedTranslationKey'),
  updateAccountDetails: (accountDetails) => set(produce(draft => {
    draft.userData.accountDetails = accountDetails;
    updatePropertyDB({ 'accountDetails': accountDetails });
  }), 'updateAccountDetails'),
  updateUID: (uid: string) => set(produce(draft => {
    draft.userData.uid = uid;
  }), 'updateUID'),
  clearAccountDetails: () => set(produce(draft => {
    delete draft.userData["accountDetails"];
    delete draft.userData["uid"];
  }), 'clearAccountDetails'),
  toggleBackgroundFlag: () => set(produce(draft => {
    const value = draft.userData.settings.backgroundFlag === false;

    draft.userData.settings.backgroundFlag = value;
    updatePropertyDB({ 'settings.backgroundFlag': value });
  }), 'toggleBackgroundFlag'),
  toggleAutoChangeBackgroundFlag: () => set(produce(draft => {
    const value = draft.userData.settings.autoChangeBackgroundFlag === false;

    draft.userData.settings.autoChangeBackgroundFlag = value;
    updatePropertyDB({ 'settings.autoChangeBackgroundFlag': value });
  }), 'toggleAutoChangeBackgroundFlag'),
  togglePlayDoaaFlag: () => set(produce(draft => {
    draft.userData.settings.playDoaaFlag = (draft.userData.settings.playDoaaFlag === false);
  }), 'togglePlayDoaaFlag'),
  toggleDateLocale: () => set(produce(draft => {
    let result = _AR_LOCALE;

    if (draft.userData.selectedDateLocale === _AR_LOCALE) {
      result = _EN_LOCALE;
    }
    draft.userData.selectedDateLocale = result;
  }), 'toggleDateLocale'),
  updateSelectedSolidColor: (value) => set(produce(draft => {
    draft.userData.settings.selectedSolidColor = value;
    updatePropertyDB({ 'settings.selectedSolidColor': value });
  }), 'updateSelectedSolidColor'),
  updateLocation: (data) => set(produce(draft => {
    draft.userData.location = data;
  }), 'updateLocation'),
  updateSelectedAdhanVoice: (adhanVoice) => set(produce(draft => {
    draft.userData.selectedAdhanVoice = adhanVoice;
  }), 'updateSelectedAdhanVoice'),
  updatePrayerTimesObject: (prayerTimesObject) => set(produce(draft => {
    draft.userData.prayerTimesObject = prayerTimesObject;
  }), 'updatePrayerTimesObject'),
  updateAzkarDayNightTracker: (index, type) => set(produce(draft => {
    if (index === undefined || !draft.userData.azkarDayNightTracker) {
      draft.userData.azkarDayNightTracker = {
        day: [],
        night: []
      };
    }
    if (index !== undefined && type) {
      if (!draft.userData.azkarDayNightTracker[type][index]) {
        draft.userData.azkarDayNightTracker[type][index] = 1;
      }
      else {
        draft.userData.azkarDayNightTracker[type][index] += 1;
      }
    }
  }), 'updateAzkarDayNightTracker'),
  updateAzkarDayNightExpire: (azkarDayNightExpire) => set(produce(draft => {
    draft.userData.azkarDayNightExpire = azkarDayNightExpire;
  }), 'updateAzkarDayNightExpire'),
  updateAudioControls: (key, value) => set(produce(draft => {
    draft.userData[key] = value;
  }), 'updateAudioControls'),
  addQuickLink: (name, domain) => set(produce(draft => {
    if (!draft.userData.quickLinksArray) {
      draft.userData.quickLinksArray = [];
    }

    draft.userData.quickLinksArray.push({ name, domain });
  }), 'addQuickLink'),
  deleteQuickLink: (index) => set(produce(draft => {
    draft.userData.quickLinksArray.splice(index, 1);
  }), 'deleteQuickLink'),
}));

