import React, { useState } from 'react';
import { Box, IconButton } from '@chakra-ui/react';
import { BsArrowRepeat } from "react-icons/bs";

import strings from "../../locales";
import Toggle from "../Toggle/Toggle.view";
import bgImg from "../../assets/imgs/bg.jpg";
import useBackgroundFunctions from '../../utils/useBackgroundFunctions';

type BackgroundTabProps = {
  className: string;
  backgroundFlag: AppState["userData"]["settings"]["backgroundFlag"];
  autoChangeBackgroundFlag: AppState["userData"]["settings"]["autoChangeBackgroundFlag"];
  selectedSolidColor: AppState["userData"]["settings"]["selectedSolidColor"];
  backgroundList: AppState["userData"]["backgroundData"]["backgroundList"];
  toggleBackgroundFlag: AppState["toggleBackgroundFlag"];
  toggleAutoChangeBackgroundFlag: AppState["toggleAutoChangeBackgroundFlag"];
  updateSelectedSolidColor: AppState["updateSelectedSolidColor"];
  updateBackgroundData: AppState["updateBackgroundData"];
}

const BackgroundTab = ({
                         className,
                         backgroundFlag,
                         autoChangeBackgroundFlag,
                         selectedSolidColor,
                         backgroundList,
                         toggleBackgroundFlag,
                         toggleAutoChangeBackgroundFlag,
                         updateSelectedSolidColor,
                         updateBackgroundData,
                       }: BackgroundTabProps) => {
  const { fetchNewBackground, setNewBackground } = useBackgroundFunctions();

  const [isImageLoading, setIsImageLoading] = useState(false);

  const shouldBeDisabled = (condition: boolean): {} => {
    if (condition) {
      return ({
        opacity: 0.4,
        cursor: "not-allowed",
        pointerEvents: 'none'
      });
    }
    return {};
  };

  const loadNewBackground = async () => {
    setIsImageLoading(true);
    if (backgroundList && backgroundList.length) {
      await setNewBackground(backgroundList);
    }
    else {
      try {
        await fetchNewBackground();
      }
      catch (error) {
        updateBackgroundData({ backgroundUrl: bgImg });
      }
    }
    setIsImageLoading(false);
  };

  return (
    <ul className={className}>
      <li className='popupMenu__row'>
        <div>
          {strings.natureBackgrounds}
        </div>
        <Toggle flag={backgroundFlag}
                onClick={toggleBackgroundFlag} />
      </li>
      <Box as="li"
           className='popupMenu__row'
           {...shouldBeDisabled(backgroundFlag === false)}>
        <div>
          {strings.autoChangeBackground}
        </div>
        <Toggle flag={autoChangeBackgroundFlag}
                onClick={toggleAutoChangeBackgroundFlag} />
      </Box>
      <Box as="li"
           className='popupMenu__row'
           {...shouldBeDisabled(backgroundFlag === false)}>
        <div>
          {strings.loadNewBackground}
        </div>
        <IconButton
          variant="ghost"
          aria-label="About me"
          fontSize="xl"
          color="var(--color-light1)"
          icon={<BsArrowRepeat />}
          isLoading={isImageLoading}
          onClick={loadNewBackground}
          {...shouldBeDisabled(isImageLoading)} />
      </Box>
      <Box as="li"
           className='popupMenu__row'
           {...shouldBeDisabled(backgroundFlag !== false)}>
        <div>
          {strings.selectedSolidColor}
        </div>
        <input type="color"
               defaultValue={selectedSolidColor}
               onChange={e => updateSelectedSolidColor(e.target.value)}
        />
      </Box>
    </ul>
  );
}

export default BackgroundTab;
