import React, { useEffect, useState } from 'react';
import axios from 'axios';
import _range from 'lodash/range';
import { Button, Text } from "@chakra-ui/react";

import useCustomToast from '../../utils/useCustomToast';
import CustomModal from '../CustomModal/CustomModal.view';
import CustomSelect from '../CustomSelect/CustomSelect.view';
import strings from '../../locales';
import { _AR } from '../../utils/constants';
import localizeNumbers from "../../utils/localizeNumbers";
import useSetVerse from "../../utils/useSetVerse";

import './ChangeVerseModal.styles.scss';

type ChangeVerseModalProps = {
  isOpen: boolean,
  onClose: () => void,
  quranData: AppState["userData"]["quranData"],
  selectedLanguageKey: AppState["userData"]["settings"]["selectedLanguageKey"],
}

type SurahNamesArray = {
  name: string;
  englishName: string;
  number: number;
};

const ChangeVerseModal = ({
                            isOpen,
                            onClose,
                            quranData,
                            selectedLanguageKey,
                          }: ChangeVerseModalProps) => {
  const toast = useCustomToast();

  const setVerse = useSetVerse();

  const [isLoading, setIsLoading] = useState(false);

  const [allSurahData, setAllSurahData] = useState([]);

  const [surahNamesArray, setSurahNamesArray] = useState([]);

  const [surahVerseNumbersArray, setSurahVerseNumbersArray] = useState([]);

  const [selectedSurahNumber, setSelectedSurahNumber] = useState(quranData.surahNumber);

  const [selectedVerseNumber, setSelectedVerseNumber] = useState(quranData.currentAyaNumber);

  useEffect(() => {
    getAllSurahData();
    /*eslint-disable react-hooks/exhaustive-deps*/
  }, [selectedLanguageKey]);

  useEffect(() => {
    getSurahVerseNumbers();
    /*eslint-disable react-hooks/exhaustive-deps*/
  }, [surahNamesArray, selectedSurahNumber]);

  useEffect(() => {
    setSelectedSurahNumber(quranData.surahNumber);
    setSelectedVerseNumber(quranData.currentAyaNumber);
  }, [quranData.surahNumber, quranData.currentAyaNumber]);

  const getAllSurahData = async () => {
    const { data } = await axios.get('data/surah.json');
    const surahNamesArray = data.map((item: SurahNamesArray) => {
      let label = item.englishName;
      let surahNumber: number | string = item.number;

      if (selectedLanguageKey === _AR) {
        label = item.name;
        surahNumber = localizeNumbers(item.number);
      }
      return {
        label: `${label} (${surahNumber})`,
        value: item.number,
      }
    });

    setAllSurahData(data);
    setSurahNamesArray(surahNamesArray);
  };

  const getSurahVerseNumbers = () => {
    if (allSurahData.length) {
      // @ts-ignore
      const totalVerses = allSurahData[selectedSurahNumber - 1].numberOfAyahs;
      const surahVerseNumbersArray = _range(1, totalVerses + 1).map(item => ({
        label: selectedLanguageKey === _AR ? localizeNumbers(item) : item,
        // @ts-ignore
        value: parseInt(item),
      }));

      // @ts-ignore
      setSurahVerseNumbersArray(surahVerseNumbersArray);
    }
  };

  const changeSurahNumber = ({ value }: { value: number }) => {
    setSelectedSurahNumber(value);
    setSelectedVerseNumber(1);
  };

  const changeSurahVerseNumber = ({ value }: { value: number }) => {
    setSelectedVerseNumber(value);
  };

  const submitChangeVerse = () => {
    if (selectedSurahNumber !== quranData.surahNumber || selectedVerseNumber !== quranData.currentAyaNumber) {
      setIsLoading(true);
      try {
        setVerse(selectedSurahNumber, selectedVerseNumber);
      }
      catch {
        const description = strings.formatString(strings.fetchError, strings.translations);
        toast({ description });
      }
      setIsLoading(false);
    }

    onClose();
  };

  const getSelectedSurahObj = (): {} => {
    // @ts-ignore
    return surahNamesArray.find(obj => obj.value === selectedSurahNumber);
  }

  const getSelectedVerseObj = (): {} => {
    // @ts-ignore
    return surahVerseNumbersArray.find(obj => obj.value === selectedVerseNumber);
  }

  return (
    <div className='ChangeVerseModal'>
      <CustomModal header={<Text className="element-has-ar">{strings.changeVerse}</Text>}
                   isOpen={isOpen}
                   onClose={onClose}
                   className={'ChangeVerseModal'}
                   footer={<Button onClick={submitChangeVerse}
                                   className="element-has-ar"
                                   isLoading={isLoading}
                                   loadingText={strings.apply}>{strings.apply}</Button>}
                   size="xs">
        <div className='ChangeVerseModal__content element-has-ar'>
          <div className='ChangeVerseModal__customSelectContent ChangeVerseModal__customSelectContent--highIndex'>
            <CustomSelect options={surahNamesArray}
                          value={getSelectedSurahObj()}
                          onChange={changeSurahNumber}
                          isSearchable
                          fullWidth />
          </div>
          <div className='ChangeVerseModal__customSelectContent'>
            <CustomSelect options={surahVerseNumbersArray}
                          value={getSelectedVerseObj()}
                          onChange={changeSurahVerseNumber}
                          isSearchable
                          fullWidth />
          </div>
        </div>
      </CustomModal>
    </div>
  );
};

export default ChangeVerseModal;
