import React from 'react';
import Select from 'react-select';
import { useStore } from "../../store";

type CustomSelectProps = {
  isSearchable?: boolean;
  fullWidth?: boolean;
  value: {};
  onChange: (arg0: any) => void;
  options: {
    label?: string,
    value?: string,
  }[];
}

const CustomSelect = ({
                        options,
                        isSearchable,
                        value,
                        onChange,
                        fullWidth,
                      }: CustomSelectProps) => {
  const darkThemeFlag = useStore(state => state.userData.settings.darkThemeFlag);

  return (
    <Select options={options}
            menuPlacement="auto"
            isSearchable={isSearchable}
            className={`popupMenu__selectContainer ${fullWidth ? 'popupMenu__selectContainer--fullWidth' : ''}`}
            theme={(theme) => {
              let obj = {
                ...theme,
                colors: {
                  ...theme.colors,
                  primary25: darkThemeFlag ? "#4A5568" : "#EDF2F7",
                },
              };

              if (darkThemeFlag) {
                obj = ({
                  ...obj,
                  colors: {
                    ...obj.colors,
                    // primary: 'var(--color-border-theme)',
                    // @ts-ignore
                    neutral0: 'var(--color-dark1-theme)',
                    neutral80: 'var(--color-light1-theme)',
                  },
                });
              }

              return obj;
            }}
            styles={{
              container: (provided) => ({
                ...provided,
                minWidth: 150,
                transformOrigin: 'right',
                zIndex: 1,
              }),
              option: /*istanbul ignore next*/
                (provided) => {
                  let obj = { ...provided };

                  if (darkThemeFlag) {
                    obj = {
                      ...obj,
                      color: 'var(--color-light1)',
                    };
                  }
                  return obj;
                },
            }}
            value={value}
            onChange={onChange} />
  );
};

export default CustomSelect;
