import React from 'react';
import { Button, VStack, HStack, Link, useDisclosure } from '@chakra-ui/react';
import { IoMdApps, IoLogoChrome, IoMdAdd, IoMdClose } from 'react-icons/io';

import AddLinkForm from '../AddLinkForm/AddLinkForm.index';
import { useStore } from "../../store";
import strings from "../../locales";
import CustomPopover from "../CustomPopover/CustomPopover.view";
import { _AR } from "../../utils/constants";
import CustomText from "../CustomText/CustomText.view";
import isEdge from "../../utils/isEdge";
import CustomIconButton from "../CustomIconButton/CustomIconButton.view";

import './QuickLinks.styles.scss'

type QuickLinksProps = {
  quickLinksArray: AppState["userData"]["quickLinksArray"];
  deleteQuickLink: AppState["deleteQuickLink"];
}


const QuickLinks = ({
                      quickLinksArray,
                      deleteQuickLink,
                    }: QuickLinksProps) => {
  const { isOpen, onOpen, onClose } = useDisclosure();

  const openChromeTab = () => {
    chrome.tabs.query({}, () => {
      chrome.tabs.create({ url: "chrome-search://local-ntp/local-ntp.html" })
    });
  }

  const openChromeApps = () => {
    chrome.tabs.query({}, () => {
      chrome.tabs.create({ url: "chrome://apps/" })
    });
  }

  const selectedLanguageKey = useStore(state => state.userData.settings.selectedLanguageKey);

  const TriggerComponent = () => {
    return (
      <Button variant="ghost"
              color="var(--color-light1)">
        <CustomText hasArabic>{strings.quickLinks}</CustomText>
      </Button>)
  };

  const TextStyles = ({
    mx: 2,
    fontSize: "sm",
  });

  const ButtonProps = () => {
    let styles = {
      variant: "link",
      my: 1,
      justifyContent: "left",
      dir: "ltr",
      width: "100%"
    };

    if (selectedLanguageKey === _AR) {
      styles = {
        ...styles,
        dir: "rtl"
      }
    }
    return styles;
  };

  const linkProps = {
    isExternal: true,
    rel: "noopener noreferrer",
    dir: "auto",
    flex: 1,
    fontSize: "sm",
    display: "flex",
    alignItems: "center",
    style: { gap: "0.5rem" }
  };

  const removeQuickLink = (index: number) => {
    const result = window.confirm("Are you sure you want to delete this link?");
    if (result) {
      deleteQuickLink(index);
    }
  }

  return (
    <div>
      <CustomPopover TriggerComponent={TriggerComponent()}
                     size="md"
                     isLazy>
        <VStack>
          {!isEdge() &&
          <Button {...ButtonProps()}
                  onClick={openChromeTab}>
            <HStack>
              <IoLogoChrome />
              <CustomText {...TextStyles}
                          hasArabic>
                {strings.chromeTab}
              </CustomText>
            </HStack>
          </Button>}
          <Button {...ButtonProps()}
                  onClick={openChromeApps}>
            <HStack>
              <IoMdApps />
              <CustomText {...TextStyles}
                          hasArabic>
                {strings.apps}
              </CustomText>
            </HStack>
          </Button>
          {quickLinksArray.map((data, index) =>
            <HStack w="100%"
                    key={index}
                    className="QuickLinks__row">
              <Link {...linkProps}
                    href={`https://${data.domain}`}>
                <img src={`https://s2.googleusercontent.com/s2/favicons?domain=${data.domain}`}
                     alt="favicon" />
                {data.name}
              </Link>
              <CustomIconButton className="QuickLinks__closeButton"
                                aria-label="remove-link"
                                size="xs"
                                icon={<IoMdClose size="16px" color={"var(--color-light3)"} />}
                                onClick={() => removeQuickLink(index)} />
            </HStack>
          )}
          <Button {...ButtonProps()}
                  onClick={onOpen}>
            <HStack>
              <IoMdAdd />
              <CustomText {...TextStyles}
                          hasArabic>
                {strings.addLink}
              </CustomText>
            </HStack>
          </Button>
        </VStack>
      </CustomPopover>
      <AddLinkForm isOpen={isOpen}
                   onClose={onClose} />
    </div>
  );
}
export default QuickLinks;
