import React, { useState } from 'react';
import { Box, Select } from "@chakra-ui/react";
import _isEqual from "lodash/isEqual";
import _debounce from "lodash/debounce";
import countries from "../../assets/countries.json";

import strings from "../../locales";
import usePrayerTimes from "../../utils/usePrayerTimes";

type SelectLocationProps = {
  location: AppState["userData"]["location"];
  updateLocation: AppState["updateLocation"];
}

type CitiesType = {
  "country": string;
  "name": string;
  "lat": string;
  "lng": string;
}[];

const SelectLocation = ({ location, updateLocation }: SelectLocationProps) => {
  const { setPrayerAlarms } = usePrayerTimes();

  const updatePrayerAlarms = _debounce(() => setPrayerAlarms(true), 500);

  const getCountry = () => {
    if (location && location.country !== "IL") {
      return location.country
    }
    return '';
  };

  const [selectedCountry, setSelectedCountry] = useState(getCountry());

  const generateCountriesList = () => {
    return Object.entries(countries).map(([key, value]) =>
      <option key={key} value={key}>{value.name}</option>
    );
  }

  const generateCitiesList = () => {
    if (selectedCountry && selectedCountry !== "IL") {
      // @ts-ignore
      const citiesArray: CitiesType = require(`../../assets/cities/${selectedCountry}.json`);

      return (
        citiesArray
          .map(item => <option key={`${item.name},${item.lat},${item.lng}`}
                               value={`${item.name},${item.lat},${item.lng}`}>{item.name}</option>)
      )
    }
    return null;
  }

  const updateLocationData = (value: string) => {
    const [city, lat, lng] = value.split(',');
    const result = {
      country: selectedCountry,
      city,
      lat: parseFloat(lat),
      lng: parseFloat(lng),
    };

    if (!_isEqual(location, result)) {
      updateLocation(result);
      updatePrayerAlarms();
    }
  }

  return (
    <>
      <li className='popupMenu__row'>
        <Box mr={12}>
          {strings.country}
        </Box>
        <Select placeholder={strings.selectCountry}
                maxW="240px"
                defaultValue={location && location.country}
                onChange={event => setSelectedCountry(event.target.value)}>
          {generateCountriesList()}
        </Select>
      </li>
      <li className='popupMenu__row'>
        <Box mr={12}>
          {strings.city}
        </Box>
        <Select placeholder={strings.selectCity}
                maxW="240px"
                defaultValue={location && `${location.city},${location.lat},${location.lng}`}
                onChange={event => updateLocationData(event.target.value)}>
          {generateCitiesList()}
        </Select>
      </li>
    </>
  );
}

export default React.memo(SelectLocation);
