import React, { useState } from 'react';
import { GoBook } from 'react-icons/go';
import { useDisclosure } from "@chakra-ui/react"

import TafseerModal from '../TafseerModal/TafseerModal.view';
import strings from "../../locales";

import './Tafseer.styles.scss';
import VerseContentIcon from "../VerseContentIcon/VerseContentIcon.view";

type TafseerProps = {
  surahNumber: number;
  ayaNumber: number;
}

const Tafseer = ({ surahNumber, ayaNumber }: TafseerProps) => {
  const { isOpen, onOpen, onClose } = useDisclosure();

  const [tafseerObj, setTafseerObj] = useState({});

  return (
    <>
      {isOpen && <TafseerModal
        surahNumber={surahNumber}
        ayaNumber={ayaNumber}
        tafseerObj={tafseerObj}
        setTafseerObj={setTafseerObj}
        isOpen={isOpen}
        onClose={onClose}
      />}
      <VerseContentIcon onClick={onOpen}
                        tooltipText={strings.tafseer}
                        data-hotkeys="tafseer">
        <GoBook />
      </VerseContentIcon>
    </>
  );
};

export default Tafseer;
