import React, { useState } from 'react';
import { Input, Button, InputGroup, InputLeftAddon } from "@chakra-ui/react";

import CustomText from "../CustomText/CustomText.view";
import useCustomToast from '../../utils/useCustomToast'
import strings from "../../locales";
import CustomModal from "../CustomModal/CustomModal.view";

type AddLinkFormProps = {
  isOpen: boolean;
  onClose: () => void;
  addQuickLink: AppState["addQuickLink"];
}

const AddLinkForm = ({ isOpen, onClose, addQuickLink }: AddLinkFormProps) => {
  const toast = useCustomToast();

  const [name, setName] = useState('');

  const [isNameValid, setIsNameValid] = useState(true);

  const [domain, setDomain] = useState('');

  const [isDomainValid, setIsDomainValid] = useState(true);

  const saveNewLink = () => {
    if (name.trim() && domain.trim()) {
      addQuickLink(name, domain);
      setName('');
      setDomain('');
      onClose();
      toast({
        description: strings.addLinkSuccess,
        status: "success"
      });
    }
    else if (!name.trim()) {
      setIsNameValid(false);
    }
    else if (!domain.trim()) {
      setIsDomainValid(false)
    }
  }

  const generateHeader = () => {
    return <CustomText hasArabic>{strings.addLink}</CustomText>;
  }

  const generateFooter = () => {
    return <Button onClick={saveNewLink}>
      <CustomText hasArabic>{strings.add}</CustomText>
    </Button>;
  }

  const inputProps = {
    isRequired: true,
    onKeyPress: (e: React.KeyboardEvent<HTMLInputElement>) => (e.key === "Enter") && saveNewLink(),
  }

  return (
    <CustomModal isOpen={isOpen}
                 size="xs"
                 header={generateHeader()}
                 footer={generateFooter()}
                 onClose={onClose}>
      <CustomText my={2} hasArabic>{strings.name}:</CustomText>
      <Input value={name}
             isInvalid={!isNameValid}
             onBlur={() => setIsNameValid(!!name.trim())}
             onChange={value => setName(value.target.value)}
             autoFocus
             {...inputProps} />
      <CustomText my={2} hasArabic>{strings.link}:</CustomText>
      <InputGroup size="sm">
        <InputLeftAddon children="https://" />
        <Input value={domain}
               placeholder="example.com"
               isInvalid={!isDomainValid}
               onBlur={() => setIsDomainValid(!!domain.trim())}
               onChange={value => setDomain(value.target.value)}
               {...inputProps} />
      </InputGroup>
    </CustomModal>
  );
}

export default AddLinkForm;
