import React, { useState } from 'react';
import { Button, useDisclosure, VStack, } from "@chakra-ui/react";
import useDirection from '../../utils/useDirection';

import CustomModal from "../CustomModal/CustomModal.view";
import CustomText from "../CustomText/CustomText.view";
import strings from "../../locales";
import AdhkarDayNight from "../AdhkarDayNight/AdhkarDayNight.index";
import CustomPopover from "../CustomPopover/CustomPopover.view";
import DoaaFromQuran from "../DoaaFromQuran/DoaaFromQuran.view";

type ModalType = "adhkarDayNight" | "doaaFromQuran";

const AzkarDayNight = () => {
  const { setDirStyle } = useDirection();

  const { isOpen, onOpen, onClose } = useDisclosure();

  const [modalType, setModalType] = useState<ModalType>('adhkarDayNight');

  const TextStyles = ({
    mx: 2,
    fontSize: "sm",
  });

  const ButtonStyles = () => {
    return ({
      variant: "link",
      my: 1,
      justifyContent: "left",
      width: "100%",
    });
  };

  const TriggerComponent = () => {
    return (
      <Button variant="ghost"
              color="var(--color-light1)">
        <CustomText hasArabic>{strings.adhkar}</CustomText>
      </Button>
    );
  }

  const openModal = (type: ModalType) => {
    setModalType(type)
    onOpen();
  }

  return (
    <div>
      <CustomPopover TriggerComponent={TriggerComponent()}
                     size="md"
                     isLazy>
        <VStack>
          <Button whiteSpace="pre-wrap"
                  {...setDirStyle()}
                  {...ButtonStyles()}
                  onClick={() => openModal("adhkarDayNight")}>
            <CustomText {...TextStyles} hasArabic>
              {strings.azkarSabahMassa}
            </CustomText>
          </Button>
          <Button whiteSpace="pre-wrap"
                  {...setDirStyle()}
                  {...ButtonStyles()}
                  onClick={() => openModal("doaaFromQuran")}>
            <CustomText {...TextStyles} hasArabic>
              {strings.quranicDoaa}
            </CustomText>
          </Button>
        </VStack>
      </CustomPopover>
      <CustomModal isOpen={isOpen}
                   {...(modalType === "doaaFromQuran") && {
                     header: <CustomText hasArabic>{strings.quranicDoaa}</CustomText>
                   }}
                   size="xl"
                   onClose={onClose}
                   isScrollInside>
        {modalType === "adhkarDayNight" ?
          <AdhkarDayNight />
          : <DoaaFromQuran />}
      </CustomModal>
    </div>);
};

export default AzkarDayNight;
