import React from 'react';
import { Player } from '@lottiefiles/react-lottie-player';
import {
  SiFacebook,
  SiTwitter,
  SiYoutube,
  SiPatreon,
  SiPaypal,
} from 'react-icons/si';
import { FaStar } from 'react-icons/fa';
import {
  IconButton,
  useDisclosure,
  Link,
  Stack,
  Text,
  Avatar,
  Button,
  Divider,
  HStack,
} from "@chakra-ui/react";
import ReactGA from 'react-ga';

import {
  _FACEBOOK_LINK,
  _PATREON_LINK,
  _PAYPAL_LINK,
  _QT_FACEBOOK_LINK,
  _QURAN_TAB_CHROME_LINK,
  _TWITTER_LINK,
  _WAZAKER_ANDROID_LINK,
  _WAZAKER_IOS_LINK,
  _YOUTUBE_LINK
} from "../../utils/constants";
import strings from '../../locales/index'
import personalPic from "../../assets/imgs/personal-pic.jpg";
import CustomModal from "../CustomModal/CustomModal.view";

import './AboutMe.styles.scss'

const defaultOptions = {
  autoplay: false,
  loop: false,
  src: require("../../assets/star-burst.json"),
  style: { height: '300px', width: '300px' },
};

const AboutMe = () => {
  const { isOpen, onOpen, onClose } = useDisclosure();

  const buttonStyles = {
    fontSize: "sm",
    size: "lg",
    variant: "solid",
  }

  const linkStyles = {
    isExternal: true,
    rel: "noopener noreferrer",
    style: {
      textDecoration: "inherit",
      marginLeft: 0
    }
  };

  const QuranTabLink = () => {
    return (
      <HStack dir="ltr" mt={1}>
        <Text mr={2}>Quran Tab:</Text>
        <Link {...linkStyles}
              color="inherit"
              href={_QURAN_TAB_CHROME_LINK}
              onClick={() => trackEvent("quranTab_chrome.clicked")}>
          <Text as="u">Chrome extension</Text>
        </Link>
      </HStack>);
  };

  const WazakerLinks = () => {
    return (
      <HStack dir="ltr" mt={1}>
        <Text mr={2}>Wazaker:</Text>
        <Link {...linkStyles}
              color="inherit"
              href={_WAZAKER_IOS_LINK}
              onClick={() => trackEvent("wazaker_ios_link.clicked")}>
          <Text as="u">iOS App</Text>
        </Link>
        <Text mx={2}>-</Text>
        <Link {...linkStyles}
              color="inherit"
              href={_WAZAKER_ANDROID_LINK}
              onClick={() => trackEvent("wazaker_android_link.clicked")}>
          <Text as="u">Android App</Text>
        </Link>
      </HStack>
    );
  };

  const trackEvent = (action: string) => {
    ReactGA.event({
      category: "About Me",
      action,
    });
  }

  const openModal = () => {
    onOpen();
    trackEvent("modal.opened");
    runAnimation();
  };

  const burstRef: React.MutableRefObject<null | Player> = React.useRef(null);

  const runAnimation = () => {
    if (burstRef && burstRef.current) {
      burstRef.current.play();
    }
  };

  return (
    <div className="AboutMe">
      <div className="AboutMe__burst">
        <Player
          ref={burstRef} // set the ref to your class instance
          {...defaultOptions}
        />
      </div>
      <IconButton
        d="flex"
        aria-label="About me"
        fontSize="xl"
        isRound
        mx={2}
        color="yellow.300"
        bg="rgba(255,255,255,0.1)"
        _hover={{ bg: "rgba(255,255,255,0.16)" }}
        icon={<FaStar className="AboutMe__icon" />}
        onClick={openModal} />
      <CustomModal onClose={onClose}
                   isOpen={isOpen}>
        <div className="AboutMe__body">
          <Stack alignItems="center" py="1rem">
            <Avatar name="Mohamed Abusrea"
                    size="xl"
                    src={personalPic} />
          </Stack>
          <Text className="element-has-ar">
            {strings.whoAmI}
          </Text>
          <QuranTabLink />
          <WazakerLinks />

          <Divider my="calc(var(--default-padding)/2)" />

          <Text className="element-has-ar"
                pb="calc(var(--default-padding)/3)">{strings.supportMe}</Text>
          <HStack mt={2}
                  spacing={4}
                  justify="center"
                  dir="ltr"
                  fontFamily="var(--en-font-family)">
            {/*<Button {...buttonStyles}
                    leftIcon={<SiBuymeacoffee />}
                    color="#e4c600"
                    onClick={() => trackEvent("by_me_coffee.clicked")}>
              <Link {...linkStyles}
                    href={_BY_ME_COFFEE_LINK}>
                Buy Me a Coffee
              </Link>
            </Button>*/}
            <Button {...buttonStyles}
                    leftIcon={<SiPatreon />}
                    color="#ff6c72"
                    onClick={() => trackEvent("patreon.clicked")}>
              <Link {...linkStyles}
                    href={_PATREON_LINK}>
                Patreon
              </Link>
            </Button>
            <Button {...buttonStyles}
                    leftIcon={<SiPaypal />}
                    color="#5599e0"
                    onClick={() => trackEvent("paypal.clicked")}>
              <Link {...linkStyles}
                    href={_PAYPAL_LINK}>
                Paypal
              </Link>
            </Button>
          </HStack>

          <Divider my="calc(var(--default-padding)/2)" />

          <HStack align="center">
            <Text className="element-has-ar">{strings.findMeHere}</Text>
            <Link {...linkStyles}
                  href={_FACEBOOK_LINK}
                  onClick={() => trackEvent("facebook.clicked")}>
              <IconButton
                variant="ghost"
                aria-label="Facebook Account"
                fontSize="xl"
                color="#2D88FF"
                icon={<SiFacebook />} />
            </Link>
            <Link {...linkStyles}
                  href={_TWITTER_LINK}
                  onClick={() => trackEvent("twitter.clicked")}>
              <IconButton
                variant="ghost"
                aria-label="Twitter Account"
                fontSize="xl"
                color="#1B91DA"
                icon={<SiTwitter />} />
            </Link>
            <Link {...linkStyles}
                  href={_YOUTUBE_LINK}
                  onClick={() => trackEvent("youtube.clicked")}>
              <IconButton
                variant="ghost"
                aria-label="Youtube Account"
                fontSize="xl"
                color="#FF0102"
                icon={<SiYoutube size="1.125rem" />} />
            </Link>
          </HStack>
          <HStack align="center" isInline>
            <Text className="element-has-ar">{strings.QT_page}</Text>
            <Link {...linkStyles}
                  href={_QT_FACEBOOK_LINK}
                  onClick={() => trackEvent("QT_facebook.clicked")}>
              <IconButton
                variant="ghost"
                aria-label="Facebook Account"
                fontSize="xl"
                color="#2D88FF"
                icon={<SiFacebook />} />
            </Link>
          </HStack>
        </div>
      </CustomModal>
    </div>
  );
};

export default AboutMe;
