import React, { useState } from 'react';
import { MdSettings } from 'react-icons/md';
import classNames from 'classnames';
import { IconButton } from "@chakra-ui/react";

import SettingsPopup from '../SettingsPopup/SettingsPopup.view';

import './Settings.styles.scss';

type SettingsProps = {
  checkedChangeLog: AppState["userData"]["checkedChangeLog"],
  updateCheckedChangeLog: AppState["updateCheckedChangeLog"],
};

const Settings = ({ checkedChangeLog, updateCheckedChangeLog }: SettingsProps) => {
  const [popupMenuFlag, setPopupMenuFlag] = useState(false);

  const settingsClasses = classNames('settings', {
    'settings--hasUpdate': !checkedChangeLog,
    'settings--isActive': popupMenuFlag,
  });

  const togglePopupMenuFlag = () => {//todo combine the 2 func
    /* istanbul ignore else */
    if (!checkedChangeLog && !popupMenuFlag) {
      updateCheckedChangeLog(true);
    }

    setPopupMenuFlag(!popupMenuFlag);
  };

  return (
    <div className={settingsClasses}>
      <IconButton
        variant="ghost"
        aria-label="About me"
        fontSize="xl"
        color="var(--color-light1)"
        icon={<MdSettings />}
        onClick={togglePopupMenuFlag} />
      <SettingsPopup flag={popupMenuFlag}
                     onCancel={togglePopupMenuFlag} />
    </div>
  );
};

export default Settings;
