import React, { useEffect, useState } from 'react';
import shallow from 'zustand/shallow';
import dayjs from 'dayjs';

import { useStore } from '../../store'
import PrayerTimes from './PrayerTimes.view';

let timeNowInterval: NodeJS.Timeout;

export default () => {
  const [timeNow, setTimeNow] = useState(dayjs());

  useEffect(() => {
    timeNowInterval && clearInterval(timeNowInterval);
    timeNowInterval = setInterval(() => {
      setTimeNow(dayjs());
    }, 60000);

    return () => clearInterval(timeNowInterval);
  }, []);

  const {
    prayerMethod,
    dayNightFlag,
    clock24Flag,
    manualPrayerTimes,
    prayerTimesFlag,
    location,
    prayerTimesObject,
  } = useStore(state => ({
    prayerMethod: state.userData.settings.prayerTimes.prayerMethod,
    dayNightFlag: state.userData.settings.prayerTimes.dayNightFlag,
    clock24Flag: state.userData.settings.clock24Flag,
    manualPrayerTimes: state.userData.settings.manualPrayerTimes,
    prayerTimesFlag: state.userData.settings.prayerTimesFlag,
    location: state.userData.location,
    prayerTimesObject: state.userData.prayerTimesObject,
  }), shallow);

  const updatePrayerTimesObject = useStore(state => state.updatePrayerTimesObject);

  if (!prayerTimesFlag) {
    return null;
  }

  return (<PrayerTimes prayerMethod={prayerMethod}
                       dayNightFlag={dayNightFlag}
                       clock24Flag={clock24Flag}
                       manualPrayerTimes={manualPrayerTimes}
                       timeNow={timeNow}
                       location={location}
                       prayerTimesObject={prayerTimesObject}
                       updatePrayerTimesObject={updatePrayerTimesObject} />);
};
