import React from 'react'
import { useStore } from "../../store";
import CreditAuthor from "./CreditAuthor.view";

export default () => {
  const backgroundData = useStore(state => state.userData.backgroundData);

  const backgroundFlag = useStore(state => state.userData.settings.backgroundFlag);

  return (<CreditAuthor backgroundData={backgroundData}
                        backgroundFlag={backgroundFlag} />)
}
