import React, { useEffect, useRef, useState } from 'react';
import { Box, IconButton, Select } from "@chakra-ui/react";
import { MdVolumeUp } from "react-icons/md";

import strings from "../../locales";
import { ADHAN_VOICES_ARRAY } from "./AdhanVoice.constants";

type AdhanVoiceProps = {
  selectedAdhanVoice: AppState["userData"]["selectedAdhanVoice"];
  adhanAudioLevel: AppState["userData"]["adhanAudioLevel"];
  updateSelectedAdhanVoice: AppState["updateSelectedAdhanVoice"];
}

const AdhanVoice = ({ selectedAdhanVoice, adhanAudioLevel, updateSelectedAdhanVoice }: AdhanVoiceProps) => {
  useEffect(() => {
    if (audioRef.current) {
      audioRef.current.volume = adhanAudioLevel;
    }
  }, [adhanAudioLevel]);

  const audioRef: React.MutableRefObject<null | HTMLAudioElement> = useRef(null);

  const [isAdhanVoicePlaying, setIsAdhanVoicePlaying] = useState(false);

  const shouldShowAdhanAudioIcon = selectedAdhanVoice && selectedAdhanVoice !== 'no-audio';

  const toggleAdhanVoice = () => {
    setIsAdhanVoicePlaying(!isAdhanVoicePlaying);
  }

  const updateAdhanVoice = (adhanVoice: string) => {
    isAdhanVoicePlaying && toggleAdhanVoice();
    updateSelectedAdhanVoice(adhanVoice);
  }

  const onLoadedData = () => {
    if (audioRef.current) {
      audioRef.current.play();
    }
  };

  return (
    <li className='popupMenu__row'>
      <Box mr={12}>
        {strings.adhanVoice}
      </Box>
      {shouldShowAdhanAudioIcon &&
      <IconButton
        variant="ghost"
        aria-label="About me"
        fontSize="xl"
        color="var(--color-light1)"
        icon={<MdVolumeUp />}
        onClick={toggleAdhanVoice} />}
      <Select maxW="240px"
              defaultValue={selectedAdhanVoice}
              onChange={event => updateAdhanVoice(event.target.value)}>
        {ADHAN_VOICES_ARRAY.map(item => <option key={item} value={item}>{strings[item]}</option>)}
      </Select>
      {isAdhanVoicePlaying &&
      <audio ref={audioRef}
             onLoadedData={onLoadedData}>
        <source src={`${process.env.PUBLIC_URL}/data/adhan/${selectedAdhanVoice}.mp3`}
                type="audio/mpeg" />
      </audio>
      }
    </li>
  );
}

export default AdhanVoice;
