import React from 'react';
import { Box, Kbd } from '@chakra-ui/react'
import strings from "../../locales";
import CustomText from "../CustomText/CustomText.view";

const HotkeysTab = () => {
  const hotKeys = [
    {
      key: '',
      value: 'previousVerse',
    },
    {
      key: '',
      value: 'nextVerse',
    },
    {
      key: 'L',
      value: 'bookmarkVerse',
    },
    {
      key: 'SPACE',
      value: 'playPauseAudio',
    },
    {
      key: 'T',
      value: 'showTafseer',
    },
  ];

  return (
    <Box as="ul"
         className="popupMenu__tabContent"
         mt={1}>
      {hotKeys.map(item =>
        <li key={item.key}
            className='popupMenu__row'>
          <Kbd>{item.key}</Kbd>
          <CustomText hasArabic>
            {strings[item.value]}
          </CustomText>
        </li>)}
    </Box>
  );
};

export default HotkeysTab;
