import axios from 'axios';
import _get from 'lodash/get';

type GetVerseReturn = Omit<AppState["userData"]["quranData"], "favourites">;

type GetVerseFunction = (surahNumber: number, currentAyaNumber: number, translationKey?: string) => Promise<GetVerseReturn>;

const getVerse = async (surahNumber = 1, currentAyaNumber = 1, translationKey?: string): Promise<GetVerseReturn> => {
  if (!translationKey) {
    return await getLocalVerse(surahNumber, currentAyaNumber);
  }
  else {
    return await getTranslation(surahNumber, currentAyaNumber, translationKey);
  }
};

const getLocalVerse: GetVerseFunction = async (surahNumber, currentAyaNumber) => {
  const result = await axios.get(`data/surah/surah_${surahNumber}.json`);

  return ({
    surahName: result.data.englishName,
    surahNameAr: result.data.name,
    surahNumber,
    currentAyaNumber,
    surahEnd: result.data.numberOfAyahs,
    currentAyaText: result.data.ayahs[currentAyaNumber - 1].text,
    number: result.data.ayahs[currentAyaNumber - 1].number,
    direction: "rtl",
    juz: result.data.ayahs[currentAyaNumber - 1].juz,
    hizbQuarter: result.data.ayahs[currentAyaNumber - 1].hizbQuarter,
  });
}

const getTranslation: GetVerseFunction = async (surahNumber, currentAyaNumber, translationKey) => {
  const url = `http://api.alquran.cloud/v1/ayah/${surahNumber}:${currentAyaNumber}/${translationKey}`;

  const { data } = await axios.get(url);

  return ({
    surahName: _get(data, 'data.surah.englishName'),
    surahNameAr: _get(data, 'data.surah.name'),
    surahNumber,
    currentAyaNumber,
    surahEnd: _get(data, 'data.surah.numberOfAyahs'),
    currentAyaText: _get(data, 'data.text'),
    direction: _get(data, 'data.edition.direction', "rtl"),
    juz: _get(data, 'data.juz'),
    hizbQuarter: _get(data, 'data.hizbQuarter'),
  });
}

export default getVerse;
