import React from 'react';
import { Box } from "@chakra-ui/react";
import _debounce from 'lodash/debounce';

import strings from "../../locales";
import CustomInput from "../CustomInput/CustomInput.view";
import usePrayerTimes from "../../utils/usePrayerTimes";

type ManuelPrayerTimesKey = string | "fajr" | "dhuhr" | "asr" | "maghrib" | "isha";

type ManuelPrayerTimesSettingsProps = {
  manualPrayerTimes: AppState["userData"]["settings"]["manualPrayerTimes"];
  updateManuelPrayerTime: AppState["updateManuelPrayerTime"];
};

const prayerTimesKeys = ["fajr", "dhuhr", "asr", "maghrib", "isha"];

const ManuelPrayerTimesSettings = ({ manualPrayerTimes, updateManuelPrayerTime }: ManuelPrayerTimesSettingsProps) => {
  const { setPrayerAlarms } = usePrayerTimes();

  const updatePrayerAlarms = _debounce(() => setPrayerAlarms(true), 500);

  const updatePrayerTime = (key: string, value: string | number) => {
    updateManuelPrayerTime(key, value);
    updatePrayerAlarms();
  }

  return (
    <li className='popupMenu__column'>
      <div>{strings.adjustPrayerTimes}</div>
      <ul>
        {
          prayerTimesKeys.map((key: ManuelPrayerTimesKey) =>
            <li className='popupMenu__row'
                style={{ paddingLeft: 0, paddingRight: 0 }}
                key={key}>
              <div>{strings[key]}</div>
              <Box maxWidth="100px">
                <CustomInput value={manualPrayerTimes[key]}
                             onChange={(value) => updatePrayerTime(key, value)} />
              </Box>
            </li>)
        }
      </ul>
    </li>
  );
}

export default ManuelPrayerTimesSettings;
