import { useStore } from "../store";
import { _AR } from "./constants";

export default () => {
  const selectedLanguageKey = useStore(state => state.userData.settings.selectedLanguageKey);

  const setDirStyle = () => {
    if (selectedLanguageKey === _AR) {
      return ({ dir: "rtl" });
    }
    return {};
  };

  const isRTL = (selectedLanguageKey === _AR);

  return { setDirStyle, isRTL };
};
