import React from 'react';
import strings from "../../locales";
import { MdFavorite, MdFavoriteBorder } from "react-icons/md";
import VerseContentIcon from "../VerseContentIcon/VerseContentIcon.view";

type AddToFavButtonProps = {
  isActive: boolean;
  onClickEvent: () => void;
}

const AddToFavButton = ({ isActive, onClickEvent }: AddToFavButtonProps) => {
  return (
    <VerseContentIcon onClick={onClickEvent}
                      tooltipText={strings.addToFavourites}
                      data-hotkeys="favourite">
      {isActive
        ? <MdFavorite style={{ color: 'var(--color-red)' }} />
        : <MdFavoriteBorder />}
    </VerseContentIcon>
  );
}

export default AddToFavButton;
