import React from 'react';
import { useStore } from "../../store";
import AddLinkForm from "./AddLinkForm.view";

type AddLinkFormProps = {
  isOpen: boolean;
  onClose: () => void;
}

export default ({ isOpen, onClose }: AddLinkFormProps) => {
  const addQuickLink = useStore(state => state.addQuickLink);

  return <AddLinkForm isOpen={isOpen}
                      onClose={onClose}
                      addQuickLink={addQuickLink} />
};
