import React from 'react';
import { useStore } from '../../store';
import ManuelPrayerTimesSettings from "./ManuelPrayerTimesSettings.view";
import { _INITIAL_USER_DATA } from "../../utils/constants";

export default () => {
  const manualPrayerTimes = useStore(state => state.userData.settings.manualPrayerTimes);

  const updateManuelPrayerTime = useStore(state => state.updateManuelPrayerTime);

  return (
    <ManuelPrayerTimesSettings manualPrayerTimes={manualPrayerTimes || _INITIAL_USER_DATA.settings.manualPrayerTimes}
                               updateManuelPrayerTime={updateManuelPrayerTime} />)
}
