import React, { ReactElement } from 'react';
import {
  Popover,
  PopoverTrigger,
  PopoverContent,
  PopoverHeader,
  PopoverBody,
  PopoverArrow,
  PopoverProps
} from "@chakra-ui/react";

type CustomPopoverProps = {
  header?: string | {};
  TriggerComponent: ReactElement;
  size?: "sm" | "md" | "lg";
  children: {};
}

const getSize = (size: CustomPopoverProps["size"]) => {
  switch (size) {
    case "sm":
      return "10rem";
    case "md":
      return "15rem";
    case "lg":
      return "25rem";
  }
}

const CustomPopover = ({
                         TriggerComponent,
                         header,
                         size,
                         children,
                         ...props
                       }: CustomPopoverProps & PopoverProps) => {
  return (
    <Popover {...props}>
      <PopoverTrigger>
        {TriggerComponent}
      </PopoverTrigger>
      <PopoverContent maxW={getSize(size)}
                      m="0 auto">
        <PopoverArrow />
        {header && <PopoverHeader>{header}</PopoverHeader>}
        <PopoverBody maxHeight="300px"
                     overflow="auto">
          {children}
        </PopoverBody>
      </PopoverContent>
    </Popover>
  );
};

export default CustomPopover;
