import React from 'react';
import Favourites from './Favourites.view';
import shallow from "zustand/shallow";

import { useStore } from "../../store";

export default () => {
  const [
    favouritesData,
    selectedLanguageKey,
    favouritesFlag,
  ] = useStore(state => [
    state.userData.quranData.favourites,
    state.userData.settings.selectedLanguageKey,
    state.userData.settings.favouritesFlag,
  ], shallow);

  if (!favouritesFlag) {
    return null
  }

  return (<Favourites favouritesData={favouritesData}
                      selectedLanguageKey={selectedLanguageKey} />);
}
