import _get from "lodash/get";
import { useStore } from "../store";
import getDateString from "./getDateString";
import getNewBackground from "./getNewBackground";

export default () => {
  const backgroundIndex = useStore(state => _get(state.userData, 'backgroundData.backgroundIndex', 0));

  const updateBackgroundData = useStore(state => state.updateBackgroundData);

  const setNewBackground = async (backgroundList: []) => {
    const newBackgroundExpire = getDateString();
    const photoLength = backgroundList.length;
    const nextBackgroundIndex = (backgroundIndex < photoLength - 1) ? (backgroundIndex + 1) : 0;
    const dpr = window.devicePixelRatio;
    const width = window.innerWidth;
    const bgPath = _get(backgroundList[nextBackgroundIndex], `urls.raw`);

    updateBackgroundData({
      backgroundUrl: bgPath && `${bgPath}&w=${width}&dpr=${dpr}`,
      username: _get(backgroundList[nextBackgroundIndex], 'user.name'),
      profile_url: _get(backgroundList[nextBackgroundIndex], 'user.links.html'),
      backgroundIndex: nextBackgroundIndex,
      backgroundExpire: newBackgroundExpire,
      backgroundList: backgroundList,
    });
  }

  const fetchNewBackground = async () => {
    const result = await getNewBackground();
    const resultArray = result.data;

    await setNewBackground(resultArray);
  }

  return ({ fetchNewBackground, setNewBackground });
};
