import React from 'react';
import { Box } from '@chakra-ui/react';

import doaaList from './DoaaFromQuran.json';
import CustomText from "../../components/CustomText/CustomText.view";

type DoaaListObject = { surahName: string, text: string };

const DoaaFromQuran = () => {
  const addBottomBorder = (index: number) => {
    if (index !== doaaList.length - 1) {
      return { borderBottom: "1px solid var(--color-border-theme)" }
    }
    return {}
  }

  return (
    <div>
      {
        doaaList.map((data: DoaaListObject, index) =>
          <Box key={index}
               py="var(--default-padding)"
               {...addBottomBorder(index)}>
            <CustomText textAlign="center"
                        fontSize="22px"
                        isVerse>
              {data.text}
            </CustomText>
            <CustomText textAlign="center"
                        fontSize="18px"
                        pt="var(--default-padding)"
                        isVerse>
               {data.surahName} 
            </CustomText>
          </Box>
        )
      }
    </div>
  );
}

export default DoaaFromQuran;
