import React from 'react';
import shallow from 'zustand/shallow'

import { useStore } from '../../store';
import PrayerTab from "./PrayerTab.view";

type Props = {
  className: string;
}

export default ({ className }: Props) => {
  const [
    prayerMethod,
    dayNightFlag,
    playDoaaFlag,
    location,
  ] = useStore(state => [
    state.userData.settings.prayerTimes.prayerMethod,
    state.userData.settings.prayerTimes.dayNightFlag,
    state.userData.settings.playDoaaFlag,
    state.userData.location,
  ], shallow);

  const updatePrayerMethod = useStore(state => state.updatePrayerMethod);

  const toggleDayNightFlag = useStore(state => state.toggleDayNightFlag);

  const togglePlayDoaaFlag = useStore(state => state.togglePlayDoaaFlag);

  const updateLocation = useStore(state => state.updateLocation);

  return (<PrayerTab className={className}
                     prayerMethod={prayerMethod}
                     dayNightFlag={dayNightFlag}
                     playDoaaFlag={playDoaaFlag !== false}
                     location={location}
                     updatePrayerMethod={updatePrayerMethod}
                     toggleDayNightFlag={toggleDayNightFlag}
                     togglePlayDoaaFlag={togglePlayDoaaFlag}
                     updateLocation={updateLocation}
  />)
};
