import React from 'react';
import shallow from "zustand/shallow";

import VerseDetails from './VerseDetails.view';
import { useStore } from '../../store'

export default () => {
  const {
    quranData,
    selectedLanguageKey,
    verseFlag,
  } = useStore(state => ({
    quranData: state.userData.quranData,
    selectedLanguageKey: state.userData.settings.selectedLanguageKey,
    verseFlag: state.userData.settings.verseFlag,
  }), shallow);

  return (<VerseDetails quranData={quranData}
                        selectedLanguageKey={selectedLanguageKey}
                        verseFlag={verseFlag} />);
}
