import React, { useState, Fragment } from 'react';
import { Box } from "@chakra-ui/react";

import { useStore } from '../../store'
import {
  _GENERAL,
  _PRAYER,
  _VERSE,
  _WHATSNEW,
  _HOTKEYS,
  _BACKGROUND,
} from '../../utils/constants';
import strings from '../../locales';
import TabsTitles from '../TabsTitles/TabsTitles.view';
import PopupMenu from '../PopupMenu/PopupMenu.view';
import GeneralTab from "../GeneralTab/index";
import VerseTab from "../VerseTab/index";
import PrayerTab from "../PrayerTab/PrayerTab.index";
import WhatsNewTab from "../WhatsNewTab/WhatsNewTab.view";
import HotkeysTab from "../HotkeysTab/HotkeysTab.view";
import BackgroundTab from "../BackgroundTab/BackgroundTab.index";

type SettingsPopupProps = {
  flag: boolean,
  onCancel: () => void,
}

const SettingsPopup = ({ flag, onCancel }: SettingsPopupProps) => {
  const checkedChangeLog = useStore(state => state.userData.checkedChangeLog);

  const selectedLanguageKey = useStore(state => state.userData.settings.selectedLanguageKey);

  const menuTitles = [
    { label: strings[_GENERAL], value: _GENERAL },
    { label: strings[_VERSE], value: _VERSE },
    { label: strings[_PRAYER], value: _PRAYER },
    { label: strings[_BACKGROUND], value: _BACKGROUND },
    // {label: strings[_TODO], value: _TODO},
    { label: strings[_WHATSNEW], value: _WHATSNEW },
    { label: strings[_HOTKEYS], value: _HOTKEYS },
    // { label: strings[_CONTACT], value: _CONTACT },
  ];

  const [selectedTabKey, setSelectedTabKey] = useState(!checkedChangeLog ? _WHATSNEW : _GENERAL);

  return (
    <PopupMenu flag={flag}
               onCancel={onCancel}>
      <Fragment>
        <TabsTitles titlesArray={menuTitles}
                    selectedTitleKey={selectedTabKey}
                    setSelectedTitleKey={setSelectedTabKey}
                    selectedLanguageKey={selectedLanguageKey} />
        <Box className={'popupMenu__tabsContent'}
             width={{ base: "auto", sm: "400px" }}>
          {selectedTabKey === _GENERAL && <GeneralTab className='popupMenu__tabContent element-has-ar' />}
          {selectedTabKey === _VERSE && <VerseTab className='popupMenu__tabContent element-has-ar' />}
          {selectedTabKey === _PRAYER && <PrayerTab className='popupMenu__tabContent element-has-ar' />}
          {selectedTabKey === _BACKGROUND && <BackgroundTab className='popupMenu__tabContent element-has-ar' />}
          {/*{selectedTabKey === _TODO && <TodoTab className='popupMenu__tabContent' />}*/}
          {selectedTabKey === _WHATSNEW && <WhatsNewTab className='popupMenu__tabContent' />}
          {selectedTabKey === _HOTKEYS && <HotkeysTab />}
          {/*{selectedTabKey === _CONTACT && <ContactTab className='popupMenu__tabContent' />}*/}
        </Box>
      </Fragment>
    </PopupMenu>
  );
};

export default SettingsPopup;

