import React from 'react';

import HijriDate from "./HijriDate.view";
import { useStore } from "../../store";
import { _AR_LOCALE } from "../../utils/constants";

export default () => {
  const selectedDateLocale = useStore(state => state.userData.selectedDateLocale) || _AR_LOCALE;

  const toggleDateLocale = useStore(state => state.toggleDateLocale);

  return (<HijriDate selectedDateLocale={selectedDateLocale}
                     toggleDateLocale={toggleDateLocale} />);
};
