import React, { useEffect, useState } from 'react';
import _get from 'lodash/get';
import _isEmpty from 'lodash/isEmpty';
import adhan from 'adhan';
import classNames from 'classnames';
import dayjs from 'dayjs';
import utc from 'dayjs/plugin/utc';
import duration from 'dayjs/plugin/duration';
import customParseFormat from 'dayjs/plugin/customParseFormat';
import { Box, Button } from '@chakra-ui/react';

import strings from '../../locales';
import { _ASR, _DHUHR, _FAJR, _ISHA, _JUMAA, _MAGHRIB, _SUNRISE } from '../../utils/constants';
import isTodayFriday from '../../utils/isTodayFriday';
import convertOldCalculationMethodNames from "../../utils/convertOldCalculationMethodNames";
import CustomText from "../CustomText/CustomText.view";
import usePrayerTimes from "../../utils/usePrayerTimes";
import en from "../../locales/en";
import ar from "../../locales/ar";

import './PrayerTimes.styles.scss';

dayjs.extend(duration);
dayjs.extend(utc);
dayjs.extend(customParseFormat);

const PrayerTimes = ({
                       prayerMethod,
                       dayNightFlag,
                       clock24Flag,
                       manualPrayerTimes,
                       timeNow,
                       location,
                       prayerTimesObject,
                       updatePrayerTimesObject,
                     }: PrayerTimesProps) => {
  useEffect(() => {
    initPrayerTimes();
    /*eslint-disable react-hooks/exhaustive-deps*/
  }, [
    prayerMethod,
    dayNightFlag,
    clock24Flag,
    manualPrayerTimes,
    timeNow,
    location,
  ]);

  const { setPrayerAlarms } = usePrayerTimes();

  const [nextPrayerObject, setNextPrayerObject] = useState<NextPrayerObject>({});

  const generatePrayerTimesObject = (times: adhan.PrayerTimes) => {
    const prayerKeys = [_FAJR, _SUNRISE, _DHUHR, _ASR, _MAGHRIB, _ISHA];
    const prayerTimesObject: { [key: string]: any } = {};

    prayerKeys.forEach(key => {
      // @ts-ignore
      let timeValue = dayjs(times[key]);

      if (dayNightFlag) {
        timeValue = dayjs(timeValue).add(1, 'hour');
      }

      prayerTimesObject[key] = {
        label: (isTodayFriday(new Date()) && key === _DHUHR) ? _JUMAA : key,
        value: timeValue.format(clock24Flag ? 'HH:mm' : 'hh:mm a'),
        timeValue: timeValue.valueOf(),
        // @ts-ignore
        labelEn: en[key], //for alarms in background.js
        // @ts-ignore
        labelAr: ar[key], //for alarms in background.js
      };
    });

    updatePrayerTimesObject(prayerTimesObject);

    return prayerTimesObject;
  };

  const generateTimes = () => {
    const date = new Date();
    const coordinates = new adhan.Coordinates(location.lat, location.lng);
    const calculationMethod = convertOldCalculationMethodNames(prayerMethod);
    // @ts-ignore
    const params = adhan.CalculationMethod[calculationMethod]();

    params.highLatitudeRule = adhan.HighLatitudeRule.TwilightAngle; //fix issue of isha having same time of fajr
    params.adjustments = { ...manualPrayerTimes };

    return new adhan.PrayerTimes(coordinates, date, params);
  };

  const initPrayerTimes = async () => {
    if (location) {
      const times = generateTimes();
      const prayerTimesObject = generatePrayerTimesObject(times);

      setPrayerAlarms();
      getNextPrayer(prayerTimesObject);
    }
  };

  const getNextPrayer = (prayerTimesObject: PrayerTimesObject) => {
    //logic is on this link: https://www.notion.so/mohamedabusrea/Quran-Tab-dbf87314a7f742aabe5588d232aa6790
    const prayerKeysArray = Object.keys(prayerTimesObject);
    let diff; //hold diff between prev/next prayer
    let prayerTime; //hold the prayer time that will be compare with the timeNow
    let prevPrayerKey;

    const nextPrayerIndex = prayerKeysArray.findIndex((key) => {
      prayerTime = dayjs(prayerTimesObject[key].value, clock24Flag ? 'HH:mm' : 'hh:mm a');
      diff = prayerTime.diff(timeNow); //diff in milliseconds by default

      return diff > 0;
    });

    if (nextPrayerIndex <= 0) {
      //Next prayer is the Fajr of the following day
      prayerTime = dayjs(prayerTimesObject[prayerKeysArray[0]].value, clock24Flag ? 'HH:mm' : 'hh:mm a');

      if (nextPrayerIndex < 0) {
        prayerTime = dayjs(prayerTime).add(1, 'day');
      }
      diff = prayerTime.diff(timeNow); //diff in milliseconds by default
      prevPrayerKey = prayerKeysArray[prayerKeysArray.length - 1];
    }
    else {
      prevPrayerKey = prayerKeysArray[nextPrayerIndex - 1];
    }

    let prevPrayerTime = dayjs(prayerTimesObject[prevPrayerKey].value, clock24Flag ? 'HH:mm' : 'hh:mm a');

    if (nextPrayerIndex === 0) {
      prevPrayerTime = dayjs(prevPrayerTime).subtract(1, 'day');
    }

    // @ts-ignore
    const prevNextPrayersDiff = prayerTime.diff(prevPrayerTime);
    // @ts-ignore
    const remainingTime = dayjs.utc(prayerTime.diff(timeNow)).format('HH:mm');
    // @ts-ignore
    const remainingPercentage = Math.abs((diff / prevNextPrayersDiff) * 100);

    setNextPrayerObject({
      key: prayerKeysArray[nextPrayerIndex < 0 ? 0 : nextPrayerIndex],
      remainingTime,
      remainingPercentage,
    });
  };

  const shouldShowRedLine = () => {
    const remainingTimeSplit = _get(nextPrayerObject, 'remainingTime', '00:00').split(':');
    const remainingMinutes = dayjs.duration({
      hours: remainingTimeSplit[0],
      minutes: remainingTimeSplit[1]
    }).asMinutes();

    return remainingMinutes < 60;
  };

  const getPrayerTimesRowClasses = (key: string) => {
    return classNames('prayerTimes__row',
      'overlay',
      { 'prayerTimes__row--active': key === nextPrayerObject.key });
  }

  const getRemainingLineClasses = () => {
    return classNames('prayerTimes__remainingLine',
      { 'prayerTimes__remainingLine--red': shouldShowRedLine() })
  };

  const chooseLocation = () => {
    //I'm too lazy to pass props
    const settingsElem = document.querySelector('.settings .chakra-button');
    const prayerTabElem = document.querySelector('.settings .tabsTitles__tab--2');
    // @ts-ignore
    settingsElem && settingsElem.click();
    // @ts-ignore
    prayerTabElem && prayerTabElem.click();
  }

  if (_isEmpty(location)) {
    return (
      <Box className="prayerTimes"
           p={0}>
        <Button
          width="100px"
          height="300px"
          variant="ghost"
          color="var(--color-light1)"
          whiteSpace="inherit"
          onClick={chooseLocation}
        >
          <CustomText hasArabic>
            {strings.chooseLocation}
          </CustomText>
        </Button>
      </Box>
    )
  }

  if (_isEmpty(prayerTimesObject)) {
    console.log('prayerTimesObject is empty');
    return null;
  }

  return (
    <Box as="ul" className="prayerTimes element-has-ar" display={{ base: "none", sm: "flex" }}>
      {
        Object.keys(prayerTimesObject)
          .map(key => <li className={getPrayerTimesRowClasses(key)}
                          key={key}>
            <div className="prayerTimes__data">
              <p className="prayerTimes__title">
                {strings[prayerTimesObject[key].label]}
              </p>
              <p className="prayerTimes__value">{prayerTimesObject[key].value}</p>
            </div>
            <div className="prayerTimes__remaining">
              <p>Remaining</p>
              <p className="prayerTimes__remainingTime">{nextPrayerObject.remainingTime}</p>
            </div>
            <div className={getRemainingLineClasses()}
                 style={{ 'width': nextPrayerObject.remainingPercentage + '%' }} />
          </li>)}
    </Box>
  );
}

export default PrayerTimes;

