import React from 'react';
import shallow from "zustand/shallow";

import VerseContent from './VerseContent.view';
import { useStore } from '../../store';

export default () => {
  const {
    quranData,
    autoSwitchVerse,
    verseFlag,
    translationFlag,
    selectedLanguageKey,
  } = useStore(state => ({
    quranData: state.userData.quranData,
    autoSwitchVerse: state.userData.settings.autoSwitchVerse,
    verseFlag: state.userData.settings.verseFlag,
    translationFlag: state.userData.settings.translationFlag,
    selectedLanguageKey: state.userData.settings.selectedLanguageKey,
  }), shallow);

  const addNewSurahToFavourites = useStore(state => state.addNewSurahToFavourites);

  const addNewAyaToFavourites = useStore(state => state.addNewAyaToFavourites);

  const removeAyaFromFavourites = useStore(state => state.removeAyaFromFavourites);

  if (verseFlag === false) {
    // verseFlag could be undefined for old users
    return null;
  }

  return (<VerseContent quranData={quranData}
                        autoSwitchVerse={autoSwitchVerse}
                        translationFlag={translationFlag !== false}
                        addNewSurahToFavourites={addNewSurahToFavourites}
                        addNewAyaToFavourites={addNewAyaToFavourites}
                        removeAyaFromFavourites={removeAyaFromFavourites}
                        selectedLanguageKey={selectedLanguageKey} />)
}
