import React from 'react';
import GeneralTab from './GeneralTab.view';
import { useStore } from "../../store";

type Props = {
  className: string;
}

export default ({ className }: Props) => {
  const settings = useStore(state => state.userData.settings);

  const updateSettingsProperty = useStore(state => state.updateSettingsProperty);

  return (<GeneralTab className={className}
                      settings={settings}
                      updateSettingsProperty={updateSettingsProperty} />);
};
