import React from 'react';
import { Button, Box } from "@chakra-ui/react";

import CustomTooltip from "../CustomTooltip/CustomTooltip.view";

type VerseContentIconProps = {
  onClick: () => void;
  children: {};
  tooltipText: string;
  isLoading?: boolean;
  [key: string]: any;
}

const VerseContentIcon = ({ onClick, children, tooltipText, isLoading, ...props }: VerseContentIconProps) => {
  return (
    <Button
      variant="ghost"
      onClick={onClick}
      isLoading={isLoading}
      aria-label={tooltipText}
      {...props}>
      <CustomTooltip label={tooltipText}>
        <Box fontSize="22px">
          {children}
        </Box>
      </CustomTooltip>
    </Button>
  );
};

export default VerseContentIcon;
