import React, { useEffect, useState } from 'react';
import classNames from 'classnames';
import _isEmpty from 'lodash/isEmpty';
import _get from 'lodash/get';

import useCustomToast from '../../utils/useCustomToast';
import { JALALAYN, MUYASSAR } from '../../utils/constants';
import data from '../../locales/ar';
import CustomModal from '../CustomModal/CustomModal.view';
import getTafseer from '../../utils/getTafseer';
import strings from "../../locales";

type TafseerModalProps = {
  tafseerObj: {
    surahNumber?: number;
    ayaNumber?: number;
  };
  surahNumber: number;
  ayaNumber: number;
  setTafseerObj: (obj: any) => void;
  isOpen: boolean;
  onClose: () => void;
}

const TafseerModal = ({
                        tafseerObj,
                        surahNumber,
                        ayaNumber,
                        setTafseerObj,
                        isOpen,
                        onClose,
                      }: TafseerModalProps) => {
  const toast = useCustomToast();

  const [selectedTab, setSelectedTab] = useState(MUYASSAR);

  const [loadingFlag, setLoadingFlag] = useState(false);

  const getTafseerData = async (): Promise<void> => {
    if (_isEmpty(tafseerObj)
      || tafseerObj.surahNumber !== surahNumber
      || tafseerObj.ayaNumber !== ayaNumber) {
      setLoadingFlag(true);
      try {
        const tafseerObjResult = await getTafseer(surahNumber, ayaNumber);
        setTafseerObj(tafseerObjResult);
      }
      catch{
        const description = strings.formatString(strings.fetchError, strings.tafseer);
        toast({ description });
      }

      setLoadingFlag(false);
    }
  };

  useEffect(() => {
    /*update the tafseer when user changes the current aya using shortcuts*/
    getTafseerData();
    /*eslint-disable react-hooks/exhaustive-deps*/
  }, [surahNumber, ayaNumber]);

  const changeSelectedTab = (tabKey: string): void => {
    setSelectedTab(tabKey);
  };

  const tafseerTabClasses = (tabKey: string): string => classNames('tafseer__tab', {
    'tafseer__tab--isActive': selectedTab === tabKey,
  });

  const tafseerTabContentClasses = classNames('tafseer__tabContent', {
    'tafseer__tabContent--isLoading': loadingFlag,
  });

  return (
    <CustomModal isOpen={isOpen}
                 onClose={onClose}>
      <>
        <ul className="tafseer__tabs">
          <button
            type="button"
            className={tafseerTabClasses(MUYASSAR)}
            onClick={(): void => changeSelectedTab(MUYASSAR)}
          >
            {data.muyassar}
          </button>
          <button
            type="button"
            className={tafseerTabClasses(JALALAYN)}
            onClick={(): void => changeSelectedTab(JALALAYN)}
          >
            {data.jalalayn}
          </button>
        </ul>

        <div className={tafseerTabContentClasses}>
          {selectedTab === MUYASSAR ? _get(tafseerObj, MUYASSAR) : _get(tafseerObj, JALALAYN)}
        </div>
      </>
    </CustomModal>
  );
};

export default TafseerModal;
