import React from 'react';
import { Box, Button, useDisclosure } from '@chakra-ui/react';

import { _AR } from '../../utils/constants';
import useLocalizeNumbers from '../../utils/useLocalizeNumbers';
import ChangeVerseModal from '../ChangeVerseModal/index';
import CustomText from "../CustomText/CustomText.view";

import './VerseDetails.styles.scss';
import { BsFillInfoCircleFill } from "react-icons/bs";
import CustomTooltip from "../CustomTooltip/CustomTooltip.view";
import strings from "../../locales";

type VerseDetailsProps = {
  quranData: AppState["userData"]["quranData"];
  selectedLanguageKey: AppState["userData"]["settings"]["selectedLanguageKey"];
  verseFlag?: AppState["userData"]["settings"]["verseFlag"];
};

const VerseDetails = ({
                        quranData,
                        selectedLanguageKey,
                        verseFlag,
                      }: VerseDetailsProps) => {
  const { isOpen, onOpen, onClose } = useDisclosure();

  const { localizeNumbers } = useLocalizeNumbers();

  const quranInfoTooltip = () => {
    const { juz, hizbQuarter } = quranData;

    if (juz && hizbQuarter) {
      const hizb = Math.ceil(hizbQuarter / 4);
      const label = <>
        <CustomText hasArabic>{strings.juz}: {localizeNumbers(juz)}</CustomText>
        <CustomText hasArabic>{strings.hizb}: {localizeNumbers(hizb)}</CustomText>
      </>;

      return <CustomTooltip label={label}
                            hasArabic
                            isImmediately>
        <Box cursor="pointer" py={1}>
          <BsFillInfoCircleFill color="var(--color-light1)" />
        </Box>
      </CustomTooltip>
    }
  };

  if (verseFlag === false) {
    // verseFlag could be undefined for old users
    return null;
  }

  return (
    <>
      <Button variant="ghost"
              color="var(--color-light1)"
              onClick={onOpen}>
        <CustomText mr={2} hasArabic>
          {selectedLanguageKey === _AR ? quranData.surahNameAr : quranData.surahName}
          {' '} -
          (
          {localizeNumbers(quranData.surahEnd)}
          /
          {localizeNumbers(quranData.currentAyaNumber)}
          )
        </CustomText>
        {quranInfoTooltip()}
      </Button>
      <ChangeVerseModal isOpen={isOpen}
                        onClose={onClose} />
    </>
  );
};

export default VerseDetails;
